import Config from 'sdk/hiveel-Config'
import AjaxUtil from 'sdk/hiveel-AjaxUtil'

class UploadService {
  //  文件(必须)： file
  //  文件夹(必须)： saveFolder: DIY / SELL_CAR / CHAT / DEMAND, (MAKE / BLOG / ADVERTISEMENT 管理员）
  //  用户： saveFor (管理员)
  //  名字： fileNameWithoutType
  //  压缩： width, height
  //  单图： single
  static save(planningFile) {
    return this.saveByUrl(this.site + '/darksouls/image.json', planningFile)
  }

  // 文件(必须)： file
  // 文件夹(必须)： saveFolder: DIY / SELL_CAR / CHAT / DEMAND, (MAKE / BLOG / ADVERTISEMENT 管理员）
  // 用户： saveFor
  // 名字： fileNameWithoutType
  static saveAudio(planningFile) {
    return this.saveByUrl(this.site + '/darksouls/audio.json', planningFile)
  }

  static saveByUrl(url, planningFile) {
    if (!planningFile.file) {
      console.log('参数 file: ' + planningFile.file + ', 不正确')
      return
    }
    if (!planningFile.saveFolder) {
      console.log('参数 saveFolder: ' + planningFile.saveFolder + ', 不正确')
      return
    }

    let formData = new FormData()
    let type =
      typeof planningFile.file === 'string' &&
      planningFile.file.constructor === String
        ? 'base64'
        : 'multipartFile'
    formData.append(type, planningFile.file)
    formData.append('saveFolder', planningFile.saveFolder)
    if (planningFile.saveFor) {
      formData.append('saveFor', planningFile.saveFor)
    }
    if (planningFile.fileNameWithoutType) {
      formData.append('fileNameWithoutType', planningFile.fileNameWithoutType)
    }
    if (planningFile.width) {
      formData.append('width', planningFile.width)
    }
    if (planningFile.height) {
      formData.append('height', planningFile.height)
    }
    if (planningFile.single) {
      formData.append('single', planningFile.single)
    }
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }

  static remove(fileName) {
    if (!fileName) {
      console.log('fileName 不能为空')
      return
    }
    let url = this.site + '/member/file.json?fileName=' + fileName
    let options = {
      method: 'DELETE',
    }
    return AjaxUtil.run(url, options)
  }

  static get site() {
    return this._site
  }
  static set site(value) {
    this._site = value
  }
}

// UploadService.site = 'http://172.168.3.128:8080/upload'
UploadService.site = Config.sitebase + '/upload'
// UploadService.site = 'https://upload.hiveel.com'
export default UploadService



