<template>
  <div v-if="show" class="center" style="width: 100%; height: 100%; position: fixed; top: 0; left: 0;">
    <slot name="content"></slot>
  </div>
</template>
<script>
export default {
  data() {
    return {
    }
  },
  computed: {
    show() { return this.$store.state.popupBox.show },
  },
  methods: {
  },
}
</script>



