import AccountService from 'sdk/hiveel-AccountService'
import MemberService from 'sdk/hiveel-MemberService'
import UploadService from 'sdk/hiveel-UploadService'
import AccountManagementService from 'sdk/hiveel-AccountManagementService'
import router from '../../router'
const CLEAN_STATE = {
  auth: false,
  employee: {phoneNumber: ''},
  permission: 0,
  accountPermission: '',
  userName: '',
}

const state = Object.assign({}, CLEAN_STATE)

const actions = {
  async login({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AccountService.login(payload)
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    if (rest.data.permission !== 4 && rest.data.permission !== 5 &&rest.data.userName !== '19792000950' ) {
      rest.code = 'UNAUTHORIZED'
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    let accountInfo = await AccountManagementService.load()
    if (accountInfo.code === 'SUCCESS') {
      commit('accountPermission', 'Admin')
    } else {
      commit('accountPermission', 'readOnly')
    }
    commit('login', rest.data)
    commit('permission', rest.data.permission)
    commit('userName', rest.data.userName)
    if(rest.data.userName === '19792000950'){
      router.push({name: 'admin'})
    }else{
      router.push({name: 'mainList'})
    }
   
    // dispatch()
  },
  async logout({commit, dispatch, state, rootState}, payload, config = {}) {
    AccountService.logout()
    commit('logout')
  },
  async updateAvatar({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await UploadService.save({file: payload, saveFor: 'emplyee/' + state.employee.id, saveFolder: 'DIY', fileNameWithoutType: 'avatar', width: 300, height: 300, single: true})
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('avatar', rest.data)
  },
  async update({commit, dispatch, state, rootState}, payload, config = {}) {
    MemberService.updateNickName({nickName: state.employee.nickName})
    MemberService.updateEmail({email: state.employee.nickName})
    MemberService.updatePhone({phoneNumber: state.employee.nickName})
    commit('employee', state.employee)
  },
}

const mutations = {
  login(state, e) {
    state.auth = true
    state.employee = e
  },
  logout(state) {
    initState(state)
  },
  avatar(state, e) {
    state.employee.imageUrl = e
  },
  employee(state, e) {
    state.employee = e
  },
  permission(state, e) {
    state.permission = e
  },
  accountPermission(state, e) {
    state.accountPermission = e
  },
  userName(state, e) {
    state.userName = e
  },
}

function initState(s) {
  Object.keys(CLEAN_STATE).forEach(key => {
    s[key] = CLEAN_STATE[key]
  })
}

export default {
  namespaced: true, state, actions, mutations,
}



