import Storage from 'sdk/hiveel-Storage'
import Img from 'sdk/hiveel-Img'
import AppointmentService from 'sdk/hiveel-AppointmentService'
import AccountManagementService from 'sdk/hiveel-AccountManagementService'
import notificationService from 'sdk/hiveel-NotificationService'
export default {
  install: function(Vue) {
    Object.defineProperty(Vue.prototype, '$storage', {value: Storage})
    Object.defineProperty(Vue.prototype, '$img', {value: Img})
    Object.defineProperty(Vue.prototype, '$AppointmentService', {value: AppointmentService})
    Object.defineProperty(Vue.prototype, '$AccountManagementService', {value: AccountManagementService})
    Object.defineProperty(Vue.prototype, '$notificationService', {value: notificationService})
  },
}



