<template>
  <div class="center" style="color:red; background-color:blue">
      <div v-for="(e, i) in list" :key="'notification'+i">
          {{e}}
      </div>
  </div>
</template>
<script>
import {toLocal,toUtc} from '../../assets/String.js'
export default {
  props: ['size'],
  data() {
    return {
      list: [],
      isEmpty: false,
      loadEnd: false,
      loadNotification: false,
    }
  },
  methods: {
    async getList() {
      console.log('getList')
      this.isEmpty = false
      this.loadNotification = true
      let rest = await this.$notificationService.load({limit: 99999})
      if (rest.code !== 'SUCCESS') {
        this.loadNotification = false
        this.$store.dispatch('popup/fail', {code: rest.code})
        return
      }
      this.loadNotification = false
      if (!rest.data.length) {
        if (!this.list) {
          this.isEmpty = true
        }
        this.loadEnd = true
        return
      }

      rest.data.map(e => {
        e.date = toLocal(e.date)
      })
      this.list = rest.data
      this.$store.commit('notification/unread', this.list.reduce((a, b) => a + (b.hasRead ? 0 : 1), 0))
    },

    async goDetail(i) {
        console.log('go Detail --------------------')
    //   this.read(i)
    //   if (this.list[i].type === 'INSURANCE_BID') {
    //     this.enterRoom(i)
    //   } else if (this.list[i].type === 'COUPON_EXPIRED') {
    //     return this.$router.push({name: 'myService', params: {type: 'coupon'}})
    //   } else {
    //     return this.$router.push({name: 'mySellCarList'})
    //   }
    },

    async read(i) {
      if (this.list[i].hasRead) {
        return
      }
      let rest = await this.$notificationService.update({id: this.list[i].id})
      if (rest.code === 'SUCCESS') {
        this.list[i].hasRead = true
        this.$store.commit('notification/unread', this.list.reduce((a, b) => a + (b.hasRead ? 0 : 1), 0))
      }
    },
    async clearAllNotification() {
      this.loadNotification = true
      for (let i = 0; i < this.list.length; i++) {
        let rest = await this.$notificationService.confirmDelete({id: this.list[i].id})
        if (rest.code !== 'SUCCESS') {
          this.loadNotification = false
          this.$store.dispatch('popup/fail', {code: rest.code})
          return
        }
      }
      this.$store.commit('notification/unread', 0)
      this.list = []
      this.loadNotification = false
    },

    toggleList(e) {
      if (e) {
        this.getList()
      }
    },

  },
  created() {
    this.getList()
  },
  computed: {
    unreadNotification() { return this.$store.state.notification.unread },
  },
}
</script>



