import Vue from 'vue'
import Vuex from 'vuex'
import cachePlugin from './plugin'
import failBox from './modules/failBox'
import popupBox from './modules/popupBox'
import overview from './modules/overview'
import appointment from './modules/appointment'
import me from './modules/me'
import push from './modules/push'

import financeService from './modules/financeService'
import calendar from './modules/calendar'
import accountManagement from './modules/accountManagement'
import notification from './modules/notification'
import tabControl from './modules/tabControl'
Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    me, failBox, popupBox, overview, appointment, push, financeService, calendar, accountManagement, notification, tabControl
  },
  plugins: [cachePlugin],
})



