const CLEAN_STATE = {
  show: false,
}

const state = Object.assign({}, CLEAN_STATE)

const actions = {
  async on({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('show', true)
  },
  async off({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('show', false)
  },
}

const mutations = {
  show(state, e) {
    state.show = e
  },
}
export default {
  namespaced: true, state, actions, mutations,
}



