import NotificationService from 'sdk/hiveel-NotificationService'

const CLEAN_STATE = {
  cache: false,
  unread: 0,
}
const state = Object.assign({}, CLEAN_STATE)

const actions = {
  async loadUnread({commit, dispatch, state, rootState}, config = {}) {
    if (!rootState.me.auth) return
    let rest = await NotificationService.count({})
    if (rest.code !== 'SUCCESS') {
    }
    commit('unread', rest.data)
  },
}
const mutations = {
  unread(state, e) {
    state.unread = e
  },
  cleanAll(state) {
    initState(state)
  },
}

function initState(s) {
  Object.keys(CLEAN_STATE).forEach(key => {
    s[key] = CLEAN_STATE[key]
  })
}

export default {
  namespaced: true, state, mutations, actions,
}



