<template>
  <main>
    <section class="center" style="height: 70px; background: #fff;">
      <div style="height: auto; width: 96%; dispplay: flex; justify-content: space-between;">
        <div style="font-size: 20px; font-weight: 500;">Financial Center</div>
      </div>
    </section>
    <section style = "margin: 26px auto; width: 96%; background: #fff; display: flex; align-items: center; flex-direction: column">
    <dataTable style = "margin-top: 0; width:100%">
      <dataTableTitle slot="title" :columnList="columnList"></dataTableTitle>
      <article slot="content" class="row" style="border-bottom: 1px solid #dfdfdf;" v-for="(e, i) in list" :key="i" @click="goDetail(e)">
        <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
          <div :class="{smallHidden: columnList[0].smallHidden}" :style="{flex: columnList[0].flex}">{{e.invoiceId}}</div>
          <div :class="{smallHidden: columnList[1].smallHidden}" :style="{flex: columnList[1].flex}">${{e.invoiceAmount|number}}</div>
          <div :class="{smallHidden: columnList[2].smallHidden}" :style="{flex: columnList[2].flex}">${{e.settledAmount*0.8|number}}</div>
          <div :class="{smallHidden: columnList[3].smallHidden}" :style="{flex: columnList[2].flex}">${{e.settledAmount*0.2|number}}</div>
          <div :class="{smallHidden: columnList[4].smallHidden}" :style="{flex: columnList[2].flex}">{{e.dateTo|dateFormat}}</div>
        </section>
      </article>
      <dataTablePage slot="page" name="Invoice(s)" :total="total" :page="page" :goPage="goPage" :exportCSV="exportCSV"></dataTablePage>
    </dataTable>

    </section>
  </main>
</template>
<script>
import dataTable from 'components/dataTable'
import dataTableTitle from 'components/dataTable/title'
import dataTablePage from 'components/dataTable/page'
import {dateFormat} from '../../assets/String.js'
export default {
  data() {
    return {
      userId: '10000001',
      columnList: [
        {name: 'Invoice ID', sortBy: '', flex: 1},
        {name: 'Invoice Amount', sortBy: '', flex: 1},
        {name: 'OAS Portion', sortBy: '', flex: 1},
        {name: 'Hiveel Portion', sortBy: '', flex: 1},
        {name: 'Invoice Date', sortBy: '', flex: 1},
      ],
      goPage: (i) => {
        this.$store.dispatch('financeService/goPage', i)
      },
    }
  },
  computed: {
    total() { return this.$store.state.financeService.total },
    page() { return this.$store.state.financeService.page },
    list() { return this.$store.state.financeService.list },
  },
  filters: {
    dateFormat: function(v) {
      return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8)
    },
  },
  components: {
    dataTable, dataTableTitle, dataTablePage,
  },
  created() {
    this.setStoreType({type: '', storeId: '', userId: '10000001', limit: 10})
  },
  mounted() {
  },
  methods: {
    exportCSV(i) {
      let ctr
      let columnDelimiter = ','
      let lineDelimiter = '\n'
      let csvContent = 'data:text/csv;charset=utf-8,'
      csvContent += 'Invoice ID, Invoice Amount, OAS Portion, Hiveel Portion, Invoice Date\n'
      let keys = ['invoiceId', 'invoiceAmount', 'settledAmount0.8', 'settledAmount0.2', 'dateTo']
      this.list.forEach(function(item) {
        ctr = 0
        keys.forEach(function(key) {
          if (ctr > 0) csvContent += columnDelimiter
          if (key === 'invoiceAmount') {
            csvContent += '$' + item[key]
          } else if (key === 'settledAmount0.8') {
            csvContent += '$' + item['settledAmount'] * 0.8
          } else if (key === 'settledAmount0.2') {
            csvContent += '$' + item['settledAmount'] * 0.2
          } else if (key === 'dateTo') {
            csvContent += dateFormat(item[key])
          } else {
            csvContent += item[key]
          }
          ctr++
        })
        csvContent += lineDelimiter
      })
      let encodedUri = encodeURI(csvContent)
      let link = document.createElement('a')
      link.setAttribute('href', encodedUri)
      link.setAttribute('download', 'InvoiceDetail.csv')
      link.click()
    },
    reset() {
      this.$store.dispatch('financeService/reset', {type: this.type, storeId: this.storeId, userId: this.userId, limit: 10})
    },
    changeFilter() {
      this.$store.dispatch('financeService/changeFilter')
    },
    goDetail(e) {
      this.$router.push({name: 'serviceInvoiceDetail', params: {id: e.invoiceId}})
    },
    setStoreType(e) {
      this.$store.dispatch('financeService/setStoreType', e)
    },
  },
}
</script>
<style scoped>
input[type=radio]:checked ~ div {
  /* color: #4A90E2; */
  background-color:#F1F1F1;
}
.ant-radio-button-wrapper-checked{
  background-color: #ff0;
}
.row {
  cursor: pointer;
}
.row:hover {
  background: #e8f4fd;
  /* color: #fff; */
}
</style>



