  const CLEAN_STATE = {
    appointment: '1',
    calendar: '1',
  }
  
  const state = { ...CLEAN_STATE }
  
  const actions = {
    changeAppointment({ commit, dispatch, state, rootState }, payload, config = {}) {
      commit('appointment', payload)
    },
    changeCalendar({ commit, dispatch, state, rootState }, payload, config = {}) {
      commit('calendar', payload)
    },
    init({ commit, dispatch, state, rootState }, payload, config = {}) {
      commit('init')
    }
  }
  
  const mutations = {
    appointment(state, e) {
      state.appointment = e
    },
    calendar(state, e) {
      state.calendar = e
    },
    init(state){
      initState(state)
    }
  }
  function initState(s) {
    Object.keys(CLEAN_STATE).forEach(key => {
      s[key] = CLEAN_STATE[key]
    })
  }
  export default {
    namespaced: true, state, actions, mutations,
  }
  


