
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
import Hiveel from 'sdk/hiveel-Hiveel'
import ParamUtil from 'sdk/hiveel-ParamUtil'
export default class {
  static count(filter) {
    let url = Hiveel.site + '/push/darksouls/pushSingleMember/count.json?' + ParamUtil.createUrlEncode(filter)
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
  static load(filter) {
    filter.limitA = filter.start
    filter.limitB = filter.limit
    let url = Hiveel.site + '/push/darksouls/query/member.json?' + ParamUtil.createUrlEncode(filter)
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
}



