<template>
    <main>
        <div v-if="this.permission === 4"><carService></carService></div>
        <div v-else-if="this.userName === 'hiveeltradein' || this.userName === 'service5'"><tradeIn></tradeIn></div>
        <div v-else-if="this.userName === 'bidlanetest1' || this.userName === 'hiveelbidlane'" ><bidlane userId="10000003"></bidlane></div>
        <div v-else>
          <section class="center" style="height: 70px; background: #fff;">
            <div style="height: auto; width: 96%; dispplay: flex; justify-content: space-between;">
              <div style="font-size: 20px; font-weight: 500;">Financial Center</div>
            </div>
          </section>
          <section style = "margin: 26px auto; width: 96%; background: #fff; display: flex; align-items: center; flex-direction: column">
            <div style="width: 100%; height:60px; padding-left: 26px; background: #fff; display: flex; align-items: center; border-bottom: 1px solid #E9E9E9; font-size:16px; font-weight:500; ">Invoice</div>
            <div class="f-12" style ="height: 32px; margin: 35px 0">You account don't have permission to access financial center</div>
          </section>
        </div>
    </main>
</template>
<script>
import bidlane from './bidlaneFinancialCenter'
import tradeIn from './tradeInFinancialCenter'
import carService from './serviceFinancialCenter'
export default {
  data() {
    return {
    }
  },
  components: {
    tradeIn, carService,bidlane
  },
  computed: {
    permission() { return this.$store.state.me.permission },
    userName(){return this.$store.state.me.userName}
  },
  created() {
  },
  mounted() {
  },
  methods: {
  },
}
</script>



