import AppointmentService from 'sdk/hiveel-AppointmentService'

const CLEAN_STATE = {
  sellCar: {
    total: 0,
    pending: 0,
    rejected: 0,
    listed: 0,
    sold: 0,
    perc: {
      pending: 0,
      rejected: 0,
      listed: 0,
      sold: 0,
    },
    today: {
      pending: 0,
      approved: 0,
      rejected: 0,
    },
  },
}

const state = Object.assign({}, CLEAN_STATE)

const actions = {
  async countSellCar({commit, dispatch, state, rootState}, payload, config = {}) {
    dispatch('countByPending')
    dispatch('countByRejected')
    dispatch('countByListed')
    dispatch('countBySold')
    dispatch('countTodayByPending')
    dispatch('countTodayByRejected')
    dispatch('countTodayByApproved')
  },

  async countByPending({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.countByReviewStatus({status: 'WAIT'})
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('countByPending', rest.data)
    commit('countSellCar')
  },
  async countByRejected({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.countByReviewStatus({status: 'FAIL'})
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('countByRejected', rest.data)
    commit('countSellCar')
  },
  async countByListed({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.countBySellingStatus({status: 'SELLING'})
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('countByListed', rest.data)
    commit('countSellCar')
  },
  async countBySold({commit, dispatch, state, rootState}, payload, config = {}) {
    let soldRest = await AppointmentService.countBySellingStatus({status: 'SOLD_OUT'})
    if (soldRest.code !== 'SUCCESS') {
      dispatch('failBox/fail', soldRest, {root: true})
      return
    }
    let underRest = await AppointmentService.countBySellingStatus({status: 'UNDER_SHELF'})
    if (underRest.code !== 'SUCCESS') {
      dispatch('failBox/fail', underRest, {root: true})
      return
    }
    commit('countBySold', soldRest.data + underRest.data)
    commit('countSellCar')
  },

  async countTodayByPending({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.countByReviewStatus({status: 'WAIT'})
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('countTodayByPending', rest.data)
  },
  async countTodayByApproved({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.countByReviewStatus({status: 'PASS'})
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('countTodayByApproved', rest.data)
  },
  async countTodayByRejected({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.countByReviewStatus({status: 'FAIL'})
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('countTodayByRejected', rest.data)
  },
}
const mutations = {
  countByPending(state, e) {
    state.sellCar.pending = e
  },
  countByRejected(state, e) {
    state.sellCar.rejected = e
  },
  countByListed(state, e) {
    state.sellCar.listed = e
  },
  countBySold(state, e) {
    state.sellCar.sold = e
  },
  countSellCar(state, e) {
    state.sellCar.total = state.sellCar.pending + state.sellCar.rejected + state.sellCar.listed + state.sellCar.sold
    state.sellCar.perc.pending = Math.round(state.sellCar.pending / state.sellCar.total * 100)
    state.sellCar.perc.rejected = Math.round(state.sellCar.rejected / state.sellCar.total * 100)
    state.sellCar.perc.listed = Math.round(state.sellCar.listed / state.sellCar.total * 100)
    state.sellCar.perc.sold = Math.round(state.sellCar.sold / state.sellCar.total * 100)
  },
  countTodayByPending(state, e) {
    state.sellCar.today.pending = e
  },
  countTodayByApproved(state, e) {
    state.sellCar.today.approved = e
  },
  countTodayByRejected(state, e) {
    state.sellCar.today.rejected = e
  },
}

export default {
  namespaced: true, state, actions, mutations,
}



