<template>
  <v-layout :style="{minHeight: browserHeight+'px'}">
    <v-sider breakpoint="md" collapsible :collapsed="collapsed" :trigger="false" :collapsed-width="64">
      <div class="bg-green" style="height: 64px; box-shadow: rgba(0, 21, 41, .35) 2px 0 6px 0; display: flex; align-items: center; ">
        <div style="margin-left: 20px; width: 160px; display: flex; justify-content: space-between; align-items: baseline;">
          <div class="nav-text" style="font-size: 20px; font-weight: 500; color: #fff;">Drchrono</div>
          <div class="nav-text" style="color: #fff;">Admin Panel</div>
        </div>
      </div>

      <v-menu theme="dark" mode="inline" :data="list" @item-click="go">
        <template slot-scope="{data}">
          <i v-if="data.icon" :class="'nav-icon anticon anticon-' + data.icon"></i>
          <span class="nav-text">{{data.name}}</span>
        </template>
      </v-menu>
    </v-sider>
    <v-layout>
      <v-header style="padding: 0; height: 64px; background: #fff; box-shadow: rgba(0, 21, 41, .12) 0 1px 4px 0; display: flex; justify-content: space-between; align-items: center; z-index: 1;">
        <div class="center" style="width: 60px; height: 100%;" @click="collapsed = !collapsed">
          <v-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'" style="font-weight: 600; font-size: 20px;"></v-icon>
        </div>
        <!-- <div style ="width:200px; display:flex; justify-content:space-between; align-items:center">
        <v-popover title="Title" trigger="hover" placement= "bottom" style = "margin-top:9px; height: 100%; display: flex; align-items: center; overflow: hidden; ">
            <v-icon type="bell" style="font-size: 26px; color: #10B975"></v-icon>
            <div slot="content">
              <notification></notification>
            </div>    
        </v-popover> -->
        <router-link :to="{name: 'profile'}">
          <div style="width: 135px; height: 100%; display: flex; align-items: center; overflow: hidden; text-overflow: ellipsis;">
            <div style="width: 30px; display: flex; align-items: center;"><avatar :src="me.employee.imageUrl" style="width: 24px; height: 24px; line-height: 1;"/></div>
            <div class="f-t2" style="display: flex; align-items: center;">{{me.employee.userName}}</div>
          </div>
        </router-link>
        </div>
      </v-header>
      <v-content>
        <slot name="content"></slot>
      </v-content>
      <!-- <menuFooter></menuFooter> -->
    </v-layout>
  </v-layout>
</template>
<script>
import avatar from 'components/image/avatar'
import menuFooter from 'components/menuNav/menuFooter'
import notification from 'components/notification/notification'
export default {
  data() {
    return {
      browserHeight: document.body.clientHeight,
      collapsed: false,
      list: [
        { name: 'Appointments', icon: 'user', go: 'mainList' },
        { name: 'Wait Time Analyze', icon: 'calendar', go: 'tools' },
        // { name: 'Account Management', icon: 'setting', go: 'setting' },
        // { name: 'Financial Center', icon: 'pay-circle-o', go: 'financial' },
        { name: 'Logout', icon: 'logout', go: 'logout' },
      ],
    }
  },
  components: {
    avatar, menuFooter,notification
  },
  computed: {
    me() { return this.$store.state.me },
  },
  mounted() {
    window.onresize = this.onresize
  },
  methods: {
    onresize() {
      this.browserHeight = document.body.clientHeight
    },
    go(nodeList) {
      if (nodeList[0].go === 'logout') {
        this.$store.dispatch('me/logout')
        return
      }
      this.$router.push({name: nodeList[0].go})
    },
  },
}
</script>

<style>
.nav-icon:hover {
  color: #108ee9;
}
.ant-layout-sider-collapsed .nav-icon {
  font-size: 16px;
}
.ant-layout-sider-collapsed .nav-text {
  display: none;
}
</style>



