
import CalendarService from 'sdk/hiveel-CalendarService'

const CLEAN_STATE = {
  appointmentT: [],
  blockT: [],
}

const state = Object.assign({}, CLEAN_STATE)

const actions = {
  async loadBlockTime({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await CalendarService.loadBlockTime(payload)
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    if (rest.data.length !== 0) {
      let blocktime = []
      let apptime = []
      rest.data.forEach(element => {
        if (element.blockedBy === 'MERCHANT') {
          blocktime = blocktime.concat(element.times)
        }
        if (element.blockedBy === 'CUSTOMER') {
          apptime = apptime.concat(element.times)
        }
      })
      commit('blockT', blocktime)
      commit('appointmentT', apptime)
    } else {
      commit('blockT', [])
      commit('appointmentT', [])
    }
  },
  async delete({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await CalendarService.delete(payload)
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
    }
  },
  async update({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await CalendarService.update(payload)
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
    }
  },
}

const mutations = {
// storeId(state, e){
//     state.storeId = e
// },
  appointmentT(state, e) {
    state.appointmentT = e
  },
  blockT(state, e) {
    state.blockT = e
  },
}

export default {
  namespaced: true, state, actions, mutations,
}



