<template>
  <main>
    <section class="center" style="height: 70px; background: #fff;">
      <div style="height: 75px; width: 96%; dispplay: flex; justify-content: space-between;">
          <div class="f-t1" style="margin-top:25px; font-weight: 500; font-size: 20px">Appointments</div>
      </div>
    </section>
     <div style="width: 100%; background-color: #fff; overflow: auto;">
    <v-tabs active-tab-key="1"
    @tab-click="onTabClick"
    >
    <v-tab-pane tab-key="1" tab="Onsite" >
    <div style="height:25px; background: #ececec"></div>
    <dataTable style = "margin-top:0;">
      <div slot="search" style="margin-bottom: 5px; width: 100%; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
        <!-- Search -->
        <section style="margin:10px; width: 230px; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
          <div style="width: 20%">Search:</div>
          <v-input size="lg" placeholder="Please Enter" v-model="filter.keyWord" style="width: 75%" @keyup="changeFilter" @keydown="changeFilter"/>
        </section>
        <!-- Location -->
        <section style="margin:10px; width: 230px; display: flex; justify-content: space-between; align-items: center;">
          <div style="width:20% display: flex; justify-content: center;">Location:</div>
          <v-select size="lg" placeholder="Please Choose" :data="locationList" v-model="filter.storeId" style="width: 75%" @change="changeFilter"/>
        </section>
        <!-- Status-->
        <section style="margin:10px; width: 200px; display: flex; justify-content: space-between; align-items: center;">
          <div style="width:20% display: flex; justify-content: center;">Status:</div>
          <v-select size="lg" placeholder="Please Choose" :data="statusList" v-model="filter.appointmentStatus" style="width: 75%" @change="changeFilter"/>
        </section>
        <!-- calendar-->
        <section style="margin:10px; width: 230px; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
         <DatePicker v-model="rangeDate" format="MM-DD-YYYY" lang=en range style="border-color:#E1E1E1; border-radius: 1px; font-size:14; font-weight: 400; color:#bfbfbf" confirm @confirm="changeRnage"></DatePicker>
           <!-- <v-date-picker size="lg" v-model="rangeDate" range format="yyyy/MM/dd" placeholder="Select Date" style="width: 75%" ></v-date-picker> -->
        </section>
        <!-- Search && Reset Button -->
        <section style="margin:10px; width: 185px; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
          <button class="btn-green" style="height: 32px; width: 85px; background: #10b975"  @click="changeFilter">Search</button>
          <button class="btn-white" style="height: 32px; width: 85px;" @click="reset">Reset</button>
        </section>
      </div>
      <dataTableTitle slot="title" :columnList="columnList" :changeSortBy="changeSortBy"></dataTableTitle>
      <article slot="content" class="row" style="border-bottom: 1px solid #dfdfdf; " v-for="(e, i) in list" :key="i" @click="goDetail(i)">
        <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
          <div :class="{smallHidden: columnList[0].smallHidden}" :style="{flex: columnList[0].flex}">{{e.bizOrderNo}}</div>
          <div :class="{smallHidden: columnList[1].smallHidden}" :style="{flex: columnList[1].flex}">{{e.firstName}} {{e.lastName}}</div>
          <div :class="{smallHidden: columnList[2].smallHidden}" :style="{flex: columnList[2].flex}">{{!e.carMaxImgUrl?'Onsite':'CarMax'}}</div>
          <div :class="{smallHidden: columnList[3].smallHidden}" :style="{flex: columnList[3].flex}">{{e.toStoreId|storeLocation}}</div>
          <div :class="{smallHidden: columnList[4].smallHidden}" :style="{flex: columnList[4].flex}">{{e.appointmentDateTimeStr|dateFormat}}</div>
          <div :class="{smallHidden: columnList[5].smallHidden}" :style="{flex: columnList[5].flex}">{{e.createAt|dateFormat}}</div>
          <div :style="{flex: columnList[6].flex}" :class="{
          'c-blue': e.appointmentStatus==='SERVICE_DONE',
          'c-green': e.appointmentStatus ==='VALID',
          'c-red': e.appointmentStatus==='CANCELED' || e.appointmentStatus==='INVALID'}">
            {{e.appointmentStatus|apptStatus}}
            </div>
        </section>
      </article>
      <dataTablePage slot="page" name="Vehicle(s)" :total="total" :page="page" :goPage="goPage" :exportCSV="exportCSV"></dataTablePage>
    </dataTable>
    </v-tab-pane>
    <v-tab-pane tab-key="2" tab="Carmax" >
    <div style="height:25px; background: #ececec"></div>
    <dataTable style = "margin-top:0;">
      <div slot="search" style="margin-bottom: 5px; width: 100%; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
        <!-- Search -->
        <section style="margin:10px; width: 230px; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
          <div style="width: 20%">Search:</div>
          <v-input size="lg" placeholder="Please Enter" v-model="filter.keyWord" style="width: 75%" @keyup="changeFilter" @keydown="changeFilter"/>
        </section>

        <!-- Status-->
        <!-- <section style="margin:10px; width: 200px; display: flex; justify-content: space-between; align-items: center;">
          <div style="width:20% display: flex; justify-content: center;">Status:</div>
          <v-select size="lg" placeholder="Please Choose" :data="statusList" v-model="filter.appointmentStatus" style="width: 75%" @change="changeFilter"/>
        </section> -->

        <!-- Search && Reset Button -->
        <section style="margin:10px; width: 185px; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
          <button class="btn-green" style="height: 32px; width: 85px; background: #10b975"  @click="changeFilter">Search</button>
          <button class="btn-white" style="height: 32px; width: 85px;" @click="reset">Reset</button>
        </section>
      </div>
      <dataTableTitle slot="title" :columnList="carMaxColumnList"></dataTableTitle>
      <article slot="content" class="row" style="border-bottom: 1px solid #dfdfdf; " v-for="(e, i) in list" :key="i" @click="goDetail(i)">
        <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
          <div :class="{smallHidden: carMaxColumnList[0].smallHidden}" :style="{flex: carMaxColumnList[0].flex}">{{e.bizOrderNo}}</div>
          <div :class="{smallHidden: carMaxColumnList[1].smallHidden}" :style="{flex: carMaxColumnList[1].flex}">{{e.firstName}} {{e.lastName}}</div>
          <div :class="{smallHidden: carMaxColumnList[2].smallHidden}" :style="{flex: carMaxColumnList[2].flex}">{{e.createAt|dateFormat}}</div>
          <div :style="{flex: carMaxColumnList[3].flex}" :class="{
          'c-blue': !e.quotePrice,
          'c-green':e.quotePrice}">
            {{!e.quotePrice?'Not Quote':'Quoted'}}
            </div>
        </section>
      </article>
      <dataTablePage slot="page" name="Vehicle(s)" :total="total" :page="page" :goPage="goPage" :exportCSV="exportCarmaxCSV"></dataTablePage>
    </dataTable>
    </v-tab-pane>
    </v-tabs>
     </div>
  </main>
</template>
<script>
import dataTable from 'components/dataTable'
import dataTableTitle from 'components/dataTable/title'
import dataTablePage from 'components/dataTable/page'
import {apptStatus, dateFormat, formatDateStr, time12Format} from '../../assets/String.js'
export default {
  data() {
    return {
      rangeDate: [],
      userId: '10000002',
      storeId: 'store001',
      type: 'TRADE_IN',
      tradeInType:'NONE',
      locationList: [{value: 'store001', label: 'West Covina'}, {value: 'store002', label: 'West Los Angeles'}],
      statusList: [{value: 'CANCELED', label: 'Canceled'}, {value: 'VALID', label: 'Booked'}, {value: 'SERVICE_DONE', label: 'Completed'}],
      columnList: [
        {name: 'Order#', sortBy: '', flex: 2},
        {name: 'User Name', sortBy: '', flex: 2, smallHidden: true},
        {name: 'Type', sortBy: '', flex: 2},
        {name: 'Location', sortBy: '', flex: 2},
        {name: 'Appointment Time', sortBy: '', flex: 2},
        {name: 'Upload Time', sortBy: '', flex: 2, smallHidden: true},
        {name: 'Status', sortBy: '', flex: 1},
      ],
      carMaxColumnList:[
        {name: 'Order#', sortBy: '', flex: 2},
        {name: 'User Name', sortBy: '', flex: 2, smallHidden: true},
        {name: 'Upload Time', sortBy: '', flex: 2, smallHidden: true},
        {name: 'Status', sortBy: '', flex: 1},
      ],
      goPage: (i) => {
        this.$store.dispatch('appointment/goPage', i)
      },
      changeSortBy: (v) => {
        let az = this.filter.sortBy === v ? !this.filter.sortByAZ : false
        this.$store.dispatch('appointment/changeSort', {sortBy: v, sortByAZ: az})
      },
    }
  },
  computed: {
    total() { return this.$store.state.appointment.total },
    page() { return this.$store.state.appointment.page },
    filter() { return this.$store.state.appointment.filter },
    list() { return this.$store.state.appointment.list },
    pageFilter() { return this.$store.state.appointment.pageFilter },
  },
  components: {
    dataTable, dataTableTitle, dataTablePage,
  },

  created() {
    this.setStoreType({type: 'TRADE_IN', storeId: '', userId: '10000002', limit: 10, tradeInType: this.tradeInType,})
  },
  mounted() {
  },
  filters: {
    dateFormat: function(v) {
      return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8) + ' ' + time12Format(v.slice(8, 12))
    },
    storeLocation: function(v) {
      return v === 'store001' ? 'West Covina' : v === 'store002' ? 'West Los Angeles' : 'On site service'
    },
  },
  methods: {
    onTabClick(tabKey) {
      this.tradeInType = tabKey === '1' ? 'NONE' : 'CARMAX'
      this.reset()
    },
    exportCarmaxCSV: async function(i) {
      let result = await this.$AppointmentService.load({
        type: this.type,
        storeId: '',
        userId: '10000002',
        ...this.filter,
        limit: 1000,
        tradeInType: 'CARMAX',
      })
      if (result.msg === 'SUCCESS') {
        let ctr, keys, columnDelimiter, lineDelimiter, data
        let csvContent
        data = result.data.data || null
        if (data == null || !data.length) {
          return null
        }
        columnDelimiter = ','
        lineDelimiter = '\n'
        csvContent = 'data:text/csv;charset=utf-8,'
        csvContent += 'Order#,Appointment#,firstName,LastName,lastUpdateAt,Status\n'
        keys = ['bizOrderNo', 'appointmentId', 'firstName', 'lastName', 'lastUpdateAt','quotePrice']
        data.forEach(function(item) {
          ctr = 0
          keys.forEach(function(key) {
            if (ctr > 0) csvContent += columnDelimiter
            if (key === 'appointmentStatus') {
              csvContent += apptStatus(item[key])
            } else if (key === 'appointmentDateTimeStr' || key === 'lastUpdateAt') {
              csvContent += dateFormat(item[key])
            } else if (key === 'quotePrice'){
              csvContent += !item[key]?'Not Quote':'Quoted'
            } else {
              csvContent += item[key]
            }
            ctr++
          })
          csvContent += lineDelimiter
        })
        let encodedUri = encodeURI(csvContent)
        let link = document.createElement('a')
        link.setAttribute('href', encodedUri)
        link.setAttribute('download', this.type + '.csv')
        link.click()
      }
    },
    exportCSV: async function(i) {
      let result = await this.$AppointmentService.load({
        type: this.type,
        storeId: '',
        userId: '10000002',
        ...this.filter,
        limit: 1000,
        tradeInType: 'NONE',
      })
      if (result.msg === 'SUCCESS') {
        let ctr, keys, columnDelimiter, lineDelimiter, data
        let csvContent
        data = result.data.data || null
        if (data == null || !data.length) {
          return null
        }
        columnDelimiter = ','
        lineDelimiter = '\n'
        csvContent = 'data:text/csv;charset=utf-8,'
        csvContent += 'Order#,Appointment#,firstName,LastName,Location,Appointment Time,lastUpdateAt,Status\n'
        keys = ['bizOrderNo', 'appointmentId', 'firstName', 'lastName', 'toStoreId', 'appointmentDateTimeStr', 'lastUpdateAt', 'appointmentStatus']
        data.forEach(function(item) {
          ctr = 0
          keys.forEach(function(key) {
            if (ctr > 0) csvContent += columnDelimiter
            if (key === 'appointmentStatus') {
              csvContent += apptStatus(item[key])
            } else if (key === 'appointmentDateTimeStr' || key === 'lastUpdateAt') {
              csvContent += dateFormat(item[key])
            } else if (key === 'toStoreId') {
              csvContent += item[key] === 'store001' ? 'West Covina' : 'West Los Angeles'
            } else {
              csvContent += item[key]
            }
            ctr++
          })
          csvContent += lineDelimiter
        })
        let encodedUri = encodeURI(csvContent)
        let link = document.createElement('a')
        link.setAttribute('href', encodedUri)
        link.setAttribute('download', this.type + '.csv')
        link.click()
      }
    },
    changeRnage() {
      this.filter.reviewStatus = true
      this.filter.startDateStr = !this.rangeDate[0] ? '' : formatDateStr(this.rangeDate[0].toISOString())
      this.filter.endDateStr = !this.rangeDate[1] ? '' : formatDateStr(this.rangeDate[1].toISOString())
      this.$store.dispatch('appointment/changeFilter')
    },
    reset() {
      this.$store.dispatch('appointment/reset', {type: this.type, tradeInType: this.tradeInType, userId: this.userId, limit: 10})
    },
    changeFilter() {
      this.$store.dispatch('appointment/changeFilter')
    },
    goDetail(i) {
      this.$router.push({name: 'tradeinOrderDetail', params: {id: this.list[i].appointmentId, userId: this.userId}})
    },
    setStoreType(e) {
      this.$store.dispatch('appointment/setStoreType', e)
    },
  },
}
</script>
<style>
.ant-tabs-bar {
  margin-bottom: 0;
}
</style>
<style scoped>
.row {
  cursor: pointer;
}
.row:hover {
  background: #e8f4fd;
  /* color: #fff; */
}
</style>



