
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
import Hiveel from 'sdk/hiveel-Hiveel'
import ParamUtil from 'sdk/hiveel-ParamUtil'
export default class {
  static confirmDelete(notification) {
    let url = Hiveel.site + '/member/me/notification/' + notification.id + '.json'
    let options = {
      method: 'DELETE',
    }
    return AjaxUtil.run(url, options)
  }

  static update(notification) {
    let url = Hiveel.site + '/member/me/notification/' + notification.id + '.json'
    let options = {
      method: 'PUT',
    }
    return AjaxUtil.run(url, options)
  }

  static count(filter) {
    let url = Hiveel.site + '/member/me/notification/count.json?' + ParamUtil.createUrlEncode(filter)
    let options = {
      method: 'GET',
    }
    console.log('count', url)
    return AjaxUtil.run(url, options)
  }

  static load(filter) {
    let url = Hiveel.site + '/member/me/notification.json?' + ParamUtil.createUrlEncode(filter)
    let options = {
      method: 'GET',
    }
    console.log('load', url)
    return AjaxUtil.run(url, options)
  }
}



