import Vue from 'vue'
import Router from 'vue-router'
import profile from '@/view/dashboard/profile'
import mainList from '@/view/main'
import serviceOrderDetail from '@/view/main/serviceDetail'
import tradeinOrderDetail from '@/view/main/tradeinDetail'
import setting from '@/view/setting/setting'
import tools from '@/view/tools'
import financial from '@/view/financial'
import tradeinInvoiceDetail from '@/view/financial/tradeinInvoiceDetail'
import serviceInvoiceDetail from '@/view/financial/serviceInvoiceDetail'
import invoice from '@/view/financial/invoice' 
import bidlaneInvoice from '@/view/financial/bidlaneInvoice' 

import admin from '@/view/admin'
Vue.use(Router)

const router = new Router({
  routes: [
    { name: 'admin', path: '/admin', component: admin},
    { name: 'profile', path: '/profile', component: profile },
    { name: 'mainList', path: '/main', component: mainList },
    // { name: 'serviceOrderDetail', path: '/main/service/:userId/:id', component: serviceOrderDetail },
    // { name: 'tradeinOrderDetail', path: '/main/tradein/:userId/:id', component: tradeinOrderDetail },
    { name: 'setting', path: '/setting', component: setting },
    // { name: 'tools', path: '/tools', component: tools },
    // { name: 'financial', path: '/financial', component: financial },
    // { name: 'invoice', path:'/financial/invoice/:id',component:invoice},
    // { name: 'bidlaneInvoice', path:'/financial/bidlaneInvoice/:id/:invoiceId/:dueDate',component:bidlaneInvoice},
    // { name: 'tradeinInvoiceDetail', path: '/financial/tradeIn/:id', component: tradeinInvoiceDetail },
    // { name: 'serviceInvoiceDetail', path: '/financial/service/:id', component: serviceInvoiceDetail },
  ],
  mode: 'history',
})
export default router



