import AppointmentService from 'sdk/hiveel-AppointmentService'
import Config from 'sdk/hiveel-Config'
const CLEAN_STATE = {
  total: 0,
  page: {curr: 1, max: 0},
  filter: {limit: 10},
  list: [],
  detail: {},
}
const state = Object.assign({}, CLEAN_STATE)
const actions = {
  setStoreType({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('filter', payload)
    dispatch('load')
  },
  goPage({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('page', payload)
    dispatch('load')
  },
  reset({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('page', 1)
    commit('reset', payload)
    dispatch('load')
  },
  changeFilter({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('page', 1)
    commit('filter', {...state.filter, ...payload})
    dispatch('load')
  },
  async update({ commit, dispatch, state, rootState }, payload, config = {}) {
    let rest = await AppointmentService.update(payload)
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, { root: true })
      return
    }
    dispatch('loadById', { id: payload.appointmentId,userId:payload.toMchId})
  },
  async updateStatus({ commit, dispatch, state, rootState }, payload, config = {}) {
    let rest = await AppointmentService.updateStatus(payload)
    if (rest.code !== 1000) {
      dispatch('failBox/fail', rest, { root: true })
      return
    }
    dispatch('loadById', { id: payload.appointmentId,userId:payload.toMchId})
  },
  async cancelCarMax({ commit, dispatch, state, rootState }, payload, config = {}) {
    let rest = await AppointmentService.cancelCarMax(payload)
    if (rest.code !== 1000) {
      dispatch('failBox/fail', rest, { root: true })
      return
    }
    dispatch('loadById', { id: payload.appointmentId,userId:payload.toMchId})
  },
  async load({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.load({...state.filter, ...state.page, total: state.total})
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('list', rest.data.data)
    commit('total', rest.data.page.total)
  },
  async loadById({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.loadById(payload)
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    if(rest.data.carMaxImgUrl){
      rest.data.carMaxImgUrl = Config.sitebase+ '/img/'+ rest.data.carMaxImgUrl 
    }
    commit('detail', rest.data)
  },
  async quote({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AppointmentService.quote(payload)
    if (rest.msg !== 'quoted!') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    dispatch('loadById', {id:payload.appointmentId, userId:payload.toMchId})
  }
}

const mutations = {
  page(state, e) {
    state.page.curr = e
  },
  total(state, e) {
    state.total = e
    state.page.max = e / state.filter.limit + (e % state.filter.limit !== 0 ? 1 : 0)
  },
  reset(state, e) {
    state.total = 0
    state.filter = e
    state.list = []
  },
  filter(state, e) {
    state.filter = e
  },
  list(state, e) {
    state.list = e
  },
  detail(state, e) {
    state.detail = e
  },
}

export default {
  namespaced: true, state, actions, mutations,
}



