
import Storage from 'sdk/hiveel-Storage'
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
import Config from 'sdk/hiveel-Config'
export default class AccountService {
  static login(memberAccount) {
    let url
    let formData = new FormData()
    url = Config.authServer + '/auth/login'
    // url = Config.authServer + '/auth/loginWithPhone'
    formData.append('userName', memberAccount.userName)
    // formData.append('phoneNumber', memberAccount.userName)
    formData.append('password', memberAccount.password)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options).then(rest => {
      if (rest.code === 'SUCCESS') {
        Storage.set('hiveel.token', rest.data.token)
        Storage.set('hiveel.token.created_at', new Date())
        Storage.set('hiveel.refreshToken', rest.data.refreshToken)
        rest.data = this.transformHiveelMember(rest.data)
        this.keepLogin()
      }
      return rest
    })
  }

  static logout() {
    let url = Config.authServer + '/auth/logout'
    let formData = new FormData()
    formData.append('refreshToken', Storage.get('hiveel.refreshToken'))
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options).then(rest => {
      Storage.remove('hiveel.token')
      Storage.remove('hiveel.refreshToken')
      this._refreshTimer = null
      return rest
    })
  }
  static async keepLogin() {
    this._refreshTimer = Storage.get('hiveel.refreshToken') ? setTimeout(() => this.refreshToken(), Config.refreshTime) : null
  }

  static refreshToken() {
    let url = Config.authServer + '/auth/token/refresh'
    let formData = new FormData()
    formData.append('refreshToken', Storage.get('hiveel.refreshToken'))
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options).then(rest => {
      if (rest.code !== 'SUCCESS') {
        // AccountService.logout()
        return rest
      }
      Storage.set('hiveel.token', rest.data.token)
      Storage.set('hiveel.token.created_at', new Date())
      this.keepLogin()
      return rest
    })
  }

  static transformHiveelMember(e) {
    console.log(e)
    return {
      id: e.user.id,
      imageUrl: e.user.imageUrl,
      nickName: e.user.nickName,
      phoneNumber: e.user.phoneNumber,
      permission: parseInt(e.user.permission),
      userName: e.user.userName,
    }
  }
  static get refreshTimer() {
    return this._refreshTimer
  }
  static set refreshTimer(value) {
    this._refreshTimer = value
  }
}

AccountService.keepLogin()



