        <div v-if="this.userName === 'bidlanetest3' || this.userName === 'hiveelbidlane2'"><newTradeIn :storeIdList="['Burbank0']" :locationList="locationList" :userId="userId"></newTradeIn></div>
        <div v-if="this.userName === 'bidlanetest5' || this.userName === 'hiveelbidlane4'"><newTradeIn :storeIdList="['WoodlandHills0']" :locationList="locationList" :userId="userId"></newTradeIn></div>
        <div v-if="this.userName === 'bidlanetest6' || this.userName === 'hiveelbidlane5'"><newTradeIn :storeIdList="['LAX0']" :locationList="locationList" :userId="userId"></newTradeIn></div>
