        <div v-if="this.userName === 'bidlanetest10' || this.userName === 'hiveelbidlane9'"><newTradeIn :storeIdList="['CarmaxVirtual0']" :locationList="locationList" :userId="userId"></newTradeIn></div>
    </main>
</template>
<script>
import newTradeIn from './newTradeIn'
import tradeinManager from './tradeInManager'
import tradeIn from './tradeIn'
import carService from './carService'
export default {
