      {value: 'CostaMesa0', label: 'Costa Mesa'}, 
      ],
    }
  },
  components: {
    tradeIn, carService,newTradeIn,tradeinManager
  },
  computed: {
    permission() { return this.$store.state.me.permission },
    userName() {return this.$store.state.me.userName}
  },
  created() {
    //登录成功后调用接口
    //get mchid and storeId list
    //transfer storeID list to lcoation list
    //
  },
  mounted() {
  },
  methods: {
  },
}
</script>
<style>
.mx-input {
  color: rgba(0,0,0,.65)!important;
  font-size: 12px!important;
  line-height: 1.5!important;
  border: 1px solid #d9d9d9!important;
}
.mx-calendar {
  color: rgba(0,0,0,.65)!important;
}
.mx-shortcuts-wrapper .mx-shortcuts {
  color: rgba(0,0,0,.65)!important;
}
.mx-input-icon {
    display: inline-block!important;
    width: 100%!important;
    height: 100%!important;
    font-style: normal!important;
    color: rgba(0,0,0,.65)!important;
    text-align: center!important;
    cursor: pointer!important;
}
</style>



