export default class {
  static createFormData(e, formData, father) {
    formData = formData || new FormData()
    Object.keys(e).map((k) => {
      let key = father ? father + '.' + k : k
      if (e[k] instanceof Object) {
        this.createFormData(e[k], formData, key)
      } else {
        formData.append(key, e[k])
      }
    })
    return formData
  }
  static createUrlEncode(e, father) {
    console.log(e)
    return Object.keys(e).map((key) => e[key] instanceof Object ? this.createUrlEncode(e[key], key) : father ? father + '.' + key + '=' + e[key] : key + '=' + e[key]).join('&')
  }
}



