<template>
  <main style="background-color:#fff; justify-content:center; align-items: center">

    <!-- <v-tabs active-tab-key="1" @tab-click="onTabClick">
      <v-tab-pane style="flex-direction: column; justify-content:center; align-items: center" class='center' tab-key="1" tab="West Covina" > -->
    <div style="flex-direction: column; justify-content:center; align-items: center" class='center' > 
    <button class="btn-green" style="height: 32px; margin: 10px 0; width: 85px; background: #10b975;" v-on:click="getPdf()">Export PDF</button>
    <div class="row" :id="WCDOM" style="padding-top: 55px;background-color:#fff; width:840px">
    <div style="background-color:#FBFBFB;">
    <section class="center" style="background: #fff; display: flex; align-items: center; flex-direction: row; justify-content: space-between">
      <div style="width: 90%; margin: 26px auto;display: flex; justify-content: space-between;">
          <div style="width: 50%; display: flex; justify-content: space-between; flex-direction: column;">
                <div style="font-size: 20px; font-weight: 500;">Hiveel Technologies, Inc</div>
                <div style="margin-top:10px; font-size: 14px;color:#828691">1050 Lakes Drive Suite 210</div>
                <div style="font-size: 14px;color:#828691">West Covina, CA 91790</div>
                <div style="font-size: 18px; margin-top:40px">Bidlane</div>
          </div>
          <div style="width: 50%; display: flex; flex-direction: column; text-align: right">
                <div style="font-size: 32px; font-weight: 500;">INVOICE</div>
                <div style="margin-top:10px; font-size: 14px;">Invoice#&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">bidlane-{{this.$route.params.invoiceId}}</span></div>
                <div style="margin-top:10px; font-size: 14px;">Invoice Date&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">{{this.$route.params.dueDate|dateFormat}}</span></div>
                <div style="margin-top:10px; font-size: 14px;">Due Date&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">{{this.$route.params.dueDate|dateFormat2}}</span></div>
          </div>
      </div>
    </section>
    <section style = " width: 100%; background: #fff; display: flex; align-items: center; flex-direction: column;">
    <div style = "width: 100%; justify-content:center; align-items:center;display:flex; flex-direction: column;" v-for="(location, index) in storeLocations" :key="index">
       <div style="width:90%; font-size:18px;  text-align:left;">{{location|storeLocation}}  (Booked: {{listMap&&listMap[location].filter(e=>e.appointmentStatus==='SERVICE_DONE'||e.appointmentStatus==='VALID').length}}, Purchased: {{listMap&&listMap[location].filter(e=>e.appointmentStatus==='PURCHASED').length}})</div>
      <dataTable style = "margin-top: 0; margin-bottom:0; width:95%">

        <dataTableTitle slot="title" :columnList="columnList"></dataTableTitle>
        <article v-if="listMap&&listMap[location].length===0" slot="content" class="row" style="border-bottom: 1px solid #dfdfdf;">
        <div style="width:100%;text-align:center">No Data</div>
        </article>
        <article  v-if="listMap&&listMap[location].length!==0" slot="content" class="row" style="border-bottom: 1px solid #dfdfdf;" v-for="(e, i) in listMap[location]" :key="i">
        
          <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
            <div :style="{flex: columnList[0].flex}">Service</div>
            <div :style="{flex: columnList[1].flex}">{{e.appointmentId}} - {{e.firstName}} {{e.lastName}} <span style = "font-weight: bold">{{e.appointmentStatus === 'PURCHASED'?' (PURCHASED)':''}}</span></div>
            <div :style="{flex: columnList[2].flex}">${{e.bizOrder.commissionAmount}}</div>
            <div :style="{flex: columnList[3].flex}">1</div>
            <div :style="{flex: columnList[4].flex}">${{e.bizOrder.commissionAmount}}</div>
          </section>
        </article>
      </dataTable>
    </div>
          <section style="width: 90%; background: #fff; display: flex; align-items: center; flex-direction: row; justify-content:flex-end;margin-bottom:100px">
              <div style="width: 40%; flex-direction: column; ">
                <div v-for="(location, index) in storeLocations" :key="index">
                  <div style="margin: 0 auto; width:100%; height: 30px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;"> 
                      <div style="font-weight: bold; font-size:14px">{{location|storeLocation}}</div>
                      <div style="font-size:14">${{subTotal[location]}}</div>
                  </div>
                </div>
                <div style = "border-bottom: 2px solid #E7E8EC;"></div>
                <div style="margin: 0 auto; width:100%; height: 50px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;"> 
                    <div style="font-weight: bold; font-size:18px">Total Amount</div>
                    <div style="font-size:32px;font-weight:bold"> ${{totalAmount|number}}</div>
                </div> 
              </div>
      </section>
    </section>
    </div>
    </div>
    </div>
  <!-- </v-tab-pane>  
  </v-tabs> -->
  </main>
</template>
<script>
import dataTable from 'components/dataTable'
import dataTableTitle from 'components/dataTable/title'
import dataTablePage from 'components/dataTable/page'
import {dateFormat, time12Format} from '../../assets/String.js'
export default {
  data() {
    return {
      totalAmount:0,
      storeLocations:['Pasadena0','Burbank0',
      'WoodlandHills0','LAX0','WestLosAngeles0','Irvine0','CostaMesa0','CarmaxVirtual0'],
      WCDOM:'pdfDom',
      LADOM:'',
      htmlTitle:  'bidlane-'+this.$route.params.invoiceId,
      columnList: [
        {name: 'ITEM', sortBy: 'id', flex: 1},
        {name: 'DESCRIPTION', sortBy:'',flex:3},
        {name: 'UNIT PRICE', sortBy: '', flex: 1},
        {name: 'QUANTITY', sortBy: '', flex: 1},
        {name: 'AMOUNT', sortBy: '', flex: 1},
      ],
      goPage: (i) => {
        this.$store.dispatch('financeService/goPage', i)
      },
    }
  },
  computed: {
    total() { return this.$store.state.financeService.total },
    page() { return this.$store.state.financeService.page },
    listWC() { return this.$store.state.financeService.detailWC},
    listLA() { return this.$store.state.financeService.detailLA},
    listMap() {
      let obj = {}
      let subTotal = {}
      let totalAmount = 0
        if (!this.$store.state.financeService.detail) return obj
        this.storeLocations.map((location)=>{
         let list = this.$store.state.financeService.detail?this.$store.state.financeService.detail.filter(v=>
          v.toStoreId===location
        ) : {}
        let amount = 0
        list.map(e=>
        {amount += parseInt(e.bizOrder.commissionAmount)
        totalAmount+=parseInt(e.bizOrder.commissionAmount)}
        )
        subTotal[location] = amount
        obj[location] = list

      })
      this.totalAmount = totalAmount
      this.subTotal = subTotal
      return obj
    }
  },
  filters: {
    amount: function(v) {
      switch (v) {
        case 'VALID': return '30.00'
        case 'SERVICE_DONE': return '30.00'
        case 'PURCHASED': return '180.00'
        default: return '30.00'
      }
    },
    storeLocation: function(v) {
      switch (v) {
        case 'Pasadena0': return 'Pasadena'
        case 'Burbank0': return 'Burbank'
        case 'Arcadia0': return 'Arcadia'
        case 'WoodlandHills0': return 'Woodland Hills'
        case 'LAX0': return 'LAX'
        case 'WestLosAngeles0': return 'West LosAngeles'
        case 'Irvine0': return 'Irvine'
        case 'CostaMesa0': return 'Costa Mesa'
        default: return 'Carmax'
      }
    },
    dateFormat: function(v) {
      return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8)
    },
    dateFormat2: function(v) {
        let day = ''
      if(v.slice(2, 4) === '01'){
          day = '08'
      }else{
          day = '23'
      }
      return v.slice(0, 2) + '-' + day + '-' + v.slice(4, 8)
    },
  },
  components: {
    dataTable, dataTableTitle, dataTablePage,
  },

  created() {
    this.loadById(this.$route.params.id)
  },
  methods: {
    amount(e){
      switch (e) {
        case 'VALID': return '30.00'
        case 'SERVICE_DONE': return '30.00'
        case 'PURCHASED': return '180.00'
        default: return '30.00'
      }
    },
    loadById(e) {
      this.$store.dispatch('financeService/loadById', e)
    },
  },
}
</script>
<style scoped>
input[type=radio]:checked ~ div {
  /* color: #4A90E2; */
  background-color:#F1F1F1;
}
.ant-radio-button-wrapper-checked{
  background-color: #ff0;
}
.row {
  cursor: pointer;
}
.row:hover {
  background: #e8f4fd;
  /* color: #fff; */
}
</style>



