<template>
  <div style="border-radius: 50%; overflow: hidden;" :style="{width:width+'px', height: h+'px'}">
    <centerImage :src=" !src ? require('@/assets/img/avatar-default.png') : $img.site  +imgSrc"/>
  </div>
</template>
<script scoped>
import centerImage from 'components/image/centerImage'
export default {
  props: {
    width: {default: 150},
    height: {default: 0},
    src: {type: String},
  },
  computed: {
    h() { return this.height || this.width },
    imgSrc() {
      if (this.src.indexOf('facebook') > 0 || this.src === 'https://apiserver.hiveel.com/img/img/template/avatar.png') {
        return require('@/assets/img/avatar-default.png')
      } else {
        return this.src
      }
    },
  },
  components: {centerImage},
}
</script>
<style scoped>
</style>



