<template>
  <main>
    <section style="height: auto; background: #fff; display: flex; justify-content: center;">
      <div style="width: 96%;" :appointment="appointment">
        <section style = "margin: 16px 0; display:flex">
          <div class="c-grey" style="cursor: pointer" @click="goBack">Appointments /</div>
          <div style="font-weight: regular; margin-left:2px" class="f-t2"> Customer Detail</div>
        </section>
        <div class="f-t1 header-fd">
          <div style="display: flex;">
            <v-icon type="user" style="margin-right: 16px; font-size: 26px; color: #10B975"></v-icon>
            <span style="font-size: 18px;">Order #{{appointment.bizOrderNo}}</span>
          </div>
        </div>
        <div style="padding-left: 44px; display: flex; justify-content: space-between; flex-wrap: wrap-reverse;">
          <div style="display: flex; padding-top:5px; padding-bottom:15px;">
            <div>
              <div class="header-fd">
                <div style="display: flex; margin-top:9px; margin-right:27px"><span style="font-weight: 600; display: block; margin-right: 27px;" class="f-t2">Customer Name:</span>{{appointment.firstName}} {{appointment.lastName}}</div>
                <div style="display: flex; margin-top:9px;"><span style="font-weight: 600; display: block; margin-right: 27px;" class="f-t2">Created Date:</span>{{appointment.createAt|dateFormat}}</div>
              </div>
              <div style="display: flex; margin-top:9px;"><span style="font-weight: 600; display: block; margin-right: 27px; " class="f-t2">Appointment Time:</span>{{appointment.appointmentDateTimeStr|dateFormat}}</div>
            </div>
          </div>
          <div style="font-size:18px; text-align: right;" class="f-t2">Status：<span class="c-blue">{{appointment.appointmentStatus|apptStatus}}</span></div>
        </div>
      </div>
    </section>
    <section style="background: #ececec; display: flex;justify-content: center;">
      <div class="appointment" style="width: 100%">
            <div style="width:96%; margin: 0 auto; background-color: #fff; border-bottom: 1px solid #fff;">
              <!-- <div style="background-color: #fff;"> -->
                <!-- part1 -->

                <div style="margin-top: 26px; width:100%; border-bottom: 1px solid #e9e9e9;">
                  <div style="margin: 0 auto;width:96%; display: flex; justify-content: space-between;  height: 50px; align-items: center;">
                    <span class="f-t1">Customer Detail</span>
                  </div>
                </div>
                <div class="vehicle-detail" style="margin: 20px auto; width: 96%">
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px " class="f-t2">First Name:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.firstName}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Last Name:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.lastName}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Phone:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.phoneNumber|phone}}</div>
                      </div>
                    </div>
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: space-around;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Address:</span></div>
                        <div style = "flex:2; margin: 5px 0;">{{appointment.street}}, {{appointment.apt}}, {{appointment.city}}, {{appointment.state}}, {{appointment.zipcode}}</div>
                      </div>
                      <div style = "flex:2;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Email:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.email}}</div>
                      </div>
                    </div>
                </div>
              </div>
                <!-- part2 -->
              <div style="width:96%; margin: 0 auto; background-color: #fff; border-bottom: 1px solid #fff;">
                <div style="margin-top: 26px; width:100%; border-bottom: 1px solid #e9e9e9;">
                  <div style="margin: 0 auto;width:96%; display: flex; justify-content: space-between;  height: 50px; align-items: center;">
                    <span class="f-t1">Vehicle Detail</span>
                  </div>
                </div>
                <div class="vehicle-detail" style="margin: 20px auto; width: 96% ">
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">VIN:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.vin}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Make:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.make}}</div>
                      </div>
                    </div>
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Year:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.year}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Model:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.model}}</div>
                      </div>
                    </div>
                </div>
             </div>
                <!-- usage report -->
                <div style="width:96%; margin: 0 auto; background-color: #fff;  border-bottom: 1px solid #fff;">
                <div style="margin-top: 26px; width:100%;  border-bottom: 1px solid #e9e9e9;">
                  <div style="margin: 0 auto;width:96%; display: flex; justify-content: space-between; height: 50px; align-items: center;">
                    <span class="f-t1">Appoinment Detail</span>
                  </div>
                </div>
                  <div class="vehicle-detail" style="margin: 20px auto; width: 96%">
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0;"><span style="font-weight: 600; display: block; margin-right:10px ; min-width:100px" class="f-t2">Company Name:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.companyName?appointment.companyName:'N/A'}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px; min-width:100px" class="f-t2">Where to find car and keys:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.whereCarAndKey?appointment.whereCarAndKey:'N/A'}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px; min-width:100px" class="f-t2">Appointment Time:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.appointmentDateTimeStr|dateFormat}}</div>
                      </div>
                    </div>
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                    </div>
                  </div>
                </div>
            </div>
          <!-- </v-tab-pane>
        </v-tabs> -->
      <!-- </div> -->
    </section>
  </main>
</template>
<script>
import centerImage from 'components/image/centerImage'
import {time12Format} from '../../assets/String.js'
export default {
  data() {
    return {
      reviewStatusList: [{value: 'WAIT', label: 'Pending'}, {value: 'PASS', label: 'Approve'}, {value: 'FAIL', label: 'Rejected'}],
    }
  },
  computed: {
    appointment() { return this.$store.state.appointment.detail },
  },
  components: {
    centerImage,
  },
  created() {
    this.load(this.$route.params.id, this.$route.params.userId)
  },
  methods: {
    load(id, userId) {
      this.$store.dispatch('appointment/loadById', {id, userId})
    },
    goBack() {
      this.$router.go(-1)
    },
  },
  filters: {
    dateFormat: function(v) {
      return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8) + ' ' + time12Format(v.slice(8, 12))
    },
  },
  mounted() {
  },
}
</script>

<style>
.vehicle-detail {
  width: 96%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.header-fd{
display: flex;
flex-wrap: wrap;
justify-content: space-between;
width: 700px
}
.oilChange div.ant-tabs-bar {
  margin-bottom: 0;
  border-bottom-color: #f0f2f5;
}
.oilChange div.ant-tabs-nav-scroll {
  padding: 0 2%;
  background-color: #fff;
}
.oilChange div.ant-tabs-content {
  padding-top: 21px;
}
.vehicleInfoRight div .ant-input {
  border: 1px solid #fff;
}

@media screen and (max-width: 768px) {
.vehicle-detail {
  width: 96%;
  flex-direction: column;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
  .header-fd{
    width: 250px;
    flex-direction: column;
    justify-content: space-around;
  }
  .carDetailLeft,
  .carDetailright {
    width: 100%;
  }
}
</style>



