<template>
  <main style = "margin-bottom:50px">
    <section style="height: auto; background: #fff; display: flex; justify-content: center;">
      <div style="width: 96%;" :appointment="appointment">
        <section style = "margin: 16px 0; display:flex">
          <div class="c-grey" style="cursor: pointer" @click="goBack">Appointments /</div>
          <div style="font-weight: regular; margin-left:2px" class="f-t2"> Customer Detail</div>
        </section>
        <div class="f-t1 header-fd">
          <div style="display: flex;">
            <v-icon type="user" style="margin-right: 16px; font-size: 26px; color: #10B975"></v-icon>
            <span style="font-size: 18px;">Order #{{appointment.bizOrderNo}}</span>
          </div>
        </div>

        <div style="padding-left: 44px; display: flex; justify-content: space-between; flex-wrap: wrap-reverse;">
          <div style="display: flex; padding-top:5px; padding-bottom:15px;">
            <div>
              <div class="header-fd">
                <div style="display: flex; margin-top:9px; margin-right:27px"><span style="font-weight: 600; display: block; margin-right: 27px;" class="f-t2">Customer Name:</span>{{appointment.firstName}} {{appointment.lastName}}</div>
                <div style="display: flex; margin-top:9px;"><span style="font-weight: 600; display: block; margin-right: 27px;" class="f-t2">Created Date:</span>{{appointment.createAt|dateFormat}}</div>
              </div>
              <div v-if="!appointment.carMaxImgUrl"  style="display: flex; margin-top:9px;"><span style="font-weight: 600; display: block; margin-right: 27px; " class="f-t2">Appointment Time:</span>{{appointment.appointmentDateTimeStr|dateFormat}}</div>
            </div>
          </div>
          <div v-if="!appointment.carMaxImgUrl"  style="display:flex; flex-direction:column">
          <div style="font-size:18px; margin-bottom:10px ;text-align: left;" class="f-t2">Status：<span class="c-blue">{{appointment.appointmentStatus|apptStatus}}</span></div>
          <div style="display:flex; flex-direction:row;">
          <div @click.prevent="showStatusConfirm('NO_SHOW')"><v-radio v-model="radio1" label="NO_SHOW"  :disabled="appointment.appointmentStatus === 'NO_SHOW'||appointment.appointmentStatus === 'PURCHASED'">No Show</v-radio></div>
          <div @click.prevent="showStatusConfirm('PURCHASED')"><v-radio v-model="radio2" label="PURCHASED" :disabled="appointment.appointmentStatus === 'PURCHASED'||appointment.appointmentStatus === 'NO_SHOW'">Purchased</v-radio></div>
          </div>
          </div>

          <div v-if="appointment.carMaxImgUrl"  style="display:flex; flex-direction:column">
          <div style="font-size:18px; margin-bottom:10px ;text-align: left;" class="f-t2">Status：<span class="c-blue">{{appointment.appointmentStatus  === 'CANCELED'?'Canceled':appointment.appointmentStatus  === 'PURCHASED' ? 'Purchased': !appointment.quotePrice?'Not Quote':'Quoted'}}</span></div>
          <div style="display:flex; flex-direction:row; margin-bottom:20px">
          <div v-if="appointment.appointmentStatus !== 'CANCELED'" @click.prevent="showStatusConfirm('PURCHASED')"><v-radio v-model="radio2" label="PURCHASED" :disabled="appointment.appointmentStatus === 'PURCHASED'||appointment.appointmentStatus === 'NO_SHOW'">Purchased</v-radio></div>
          </div>
          </div>
        </div>
      </div>
    </section>

    <section style="background: #ececec; display: flex;justify-content: center;">
      <div class="appointment" style="width: 100%">
            <div style="width:96%; margin: 0 auto; background-color: #fff; border-bottom: 1px solid #fff;">
              <!-- <div style="background-color: #fff;"> -->
                <!-- part1 -->

                <div style="margin-top: 26px; width:100%; border-bottom: 1px solid #e9e9e9;">
                  <div style="margin: 0 auto;width:96%; display: flex; justify-content: space-between;  height: 50px; align-items: center;">
                    <span class="f-t1">Customer Detail</span>
                  </div>
                </div>
                <div class="vehicle-detail" style="margin: 20px auto; width: 96%">
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px " class="f-t2">First Name:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.firstName}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Last Name:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.lastName}}</div>
                      </div>
                    </div>
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: space-around;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Email:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.email}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Phone:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.phoneNumber|phone}}</div>
                      </div>
                    </div>
                </div>
              </div>
                <!-- part2 -->
              <div v-if="!appointment.carMaxImgUrl" style="width:96%; margin: 0 auto; background-color: #fff; border-bottom: 1px solid #fff;">
                <div style="margin-top: 26px; width:100%; border-bottom: 1px solid #e9e9e9;">
                  <div style="margin: 0 auto;width:96%; display: flex; justify-content: space-between;  height: 50px; align-items: center;">
                    <span class="f-t1">Vehicle Detail</span>
                  </div>
                </div>
                <div  class="vehicle-detail" style="margin: 20px auto; width: 96% ">
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Year:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.year}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Model:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.model}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Title Status:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.titleStatus}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Note:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.remark}}</div>
                      </div>
                    </div>
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">VIN:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.vin}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Make:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.make}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Mileage:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.mileage?appointment.mileage:'N/A'}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px" class="f-t2">Previous Offers:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.previousOfferPrice?appointment.previousOfferPrice:'N/A'}}</div>
                      </div>
                    </div>
                </div>
             </div>
                <!-- usage report -->
                <div v-if="!appointment.carMaxImgUrl" style="width:96%; margin: 0 auto; background-color: #fff;  border-bottom: 1px solid #fff;">
                <div style="margin-top: 26px; width:100%;  border-bottom: 1px solid #e9e9e9;">
                  <div style="margin: 0 auto;width:96%; display: flex; justify-content: space-between; height: 50px; align-items: center;">
                    <span class="f-t1">Appoinment Detail</span>
                  </div>
                </div>
                  <div class="vehicle-detail" style="margin: 20px auto; width: 96%">
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px; min-width:100px" class="f-t2">Appointment Time:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.appointmentDateTimeStr|dateFormat}}</div>
                      </div>
                      <div style = "flex:1;  display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px; min-width:100px" class="f-t2">Appointment Location:</span></div>
                        <div style = "flex:2; margin: 5px 0">{{appointment.toStoreId|location}}</div>
                      </div>
                    </div>
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                    </div>
                  </div>
                </div>
                                <!-- usage report -->
                <div v-if="appointment.carMaxImgUrl" style="width:96%; margin: 0 auto; background-color: #fff;  border-bottom: 1px solid #fff;">
                <div style="margin-top: 26px; width:100%;  border-bottom: 1px solid #e9e9e9;">
                  <div style="margin: 0 auto;width:96%; display: flex; justify-content: space-between; height: 50px; align-items: center;">
                    <span class="f-t1">Quote Detail</span>
                  </div>
                </div>
                  <div class="vehicle-detail" style="margin: 20px auto; width: 96%">
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                      
                      <div style = "flex:1;  display: flex; margin: 0 0 20px 0">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px; min-width:100px" class="f-t2">Car Max Offer:</span></div>
                        <img @click="onSaveType" style="width: 300px; height: 180px;" :src="appointment.carMaxImgUrl">
                      </div>
                      <div v-if="appointment.appointmentStatus !== 'CANCELED'" style = "display: flex;">
                        <div style = "flex:1; margin: 5px 0"><span style="font-weight: 600; display: block; margin-right:10px; min-width:100px" class="f-t2">Quote Price ($):</span></div>
                        <div 
                            v-if="!appointment.quotePrice" 
                        style="flex-basis: 250px;"><v-input placeholder="" v-model="quotePrice"></v-input></div>
                        <div 
                            v-if="appointment.quotePrice" 
                        style="flex-basis: 250px;"><v-input disabled v-model="appointment.quotePrice"></v-input></div>
                        <div style="width:230px;display:flex;flex-direction:row; justify-content:space-between">
                        <button 
                            v-if="!appointment.quotePrice" 
                        class="btn-green" style=" margin: 0 0 0 20px; height: 28px; width: 85px;"  @click.prevent="showConfirm(quotePrice)">Quote</button>
                                                <button 
                            v-if="!appointment.quotePrice" 
                        class="btn-red" style=" margin: 0 0 0 20px; height: 28px; width: 120px;"  @click.prevent="showCancel()">Cannot Quote</button>
                        </div>
                      </div>

                    </div>
                    <div style = "flex:1; display: flex; flex-direction: column; justify-content: center;" >
                    </div>
                  </div>
                </div>
            </div>
            <v-modal title="Alert"
             :visible="visible"
             @ok="handleOk"
             @cancel="handleCancel"
             okText='CONFIRM'
             cancelText='Cancel'
             :confirmLoading="AsyncLoading" :maskClosable="false">
                <div >Please give the reason here to customer why you cannot quote on his/her CarMax offer. (At least 30 characters)</div>
                <div style="flex-basis: 250px; max-width:300px; margin-top:10px"><v-input type="textarea" autosize placeholder="" v-model="message" @keyup="enterMessage" @keydown="enterMessage" ></v-input></div>
                <div style="margin-top:10px"><span :class="{'c-red': charLeft === 0}">{{charLeft}} </span>character(s) left</div>
            </v-modal>
            <popupBox style ="overflow: auto;">
              <div slot="content" class="center" style="width: 60%; min-width: 720px; height:800px; background: #fff; border-radius: 2px; box-shadow: #e8e8e8 0 2px 4px 0;">
                <div style="width: 90%; height: 90%; display: flex; flex-direction: column; justify-content: space-between; position: relative;">
                  <section style=" right: 0; " @click="offSaveType"><v-icon type="close" style="font-size:30px"  ></v-icon></section>
                      <img style="width: 95%; height: auto; " :src="appointment.carMaxImgUrl">
                </div>
              </div>
            </popupBox>
    </section>
  </main>
  
</template>
<script>
import popupBox from 'components/popupBox'
import {apptStatus,  time12Format} from '../../assets/String.js'
export default {
  data() {
    return {
      AsyncLoading:false,
      quotePrice:'',
      message:'',
      visible:false,
      charLeft:200,
      radio1:'',
      radio2:'',
      options: [
        {label: 'No Show', value: 'NO_SHOW'},
        {label: 'Purchased', value: 'PURCHASED'},
        ],
      imageModalVisible:true,
      reviewStatusList: [{value: 'WAIT', label: 'Pending'}, {value: 'PASS', label: 'Approve'}, {value: 'FAIL', label: 'Rejected'}],
    }
  },
  computed: {
    appointment() { 
      if(this.$store.state.appointment.detail.appointmentStatus === 'NO_SHOW'){
        this.radio1 = 'NO_SHOW'
      }else{
        this.radio1 = ''
      }
      if(this.$store.state.appointment.detail.appointmentStatus === 'PURCHASED'){
        this.radio2 = 'PURCHASED'
      }else{
        this.radio2 = ''
      }
      return this.$store.state.appointment.detail },
    },
  components: {
    popupBox
  },
  created() {
    this.load(this.$route.params.id, this.$route.params.userId)
  },
  methods: {
    onSaveType() {
      this.$store.dispatch('popupBox/on')
    },
    offSaveType() {
      this.$store.dispatch('popupBox/off')
    },
    showConfirm(price) {
      this.$modal.confirm({
        title: 'Alert',
        content: 'Your quote is $'+ price +' ,This action cannot be reversed. Press "Confirm" to delete this account',
        okText: 'CONFIRM',
        cancelText: 'Cancel',
        onOk: () => this.quote(price),
        onCancel: function() {},
      })
    },
    showCancel() {
      this.visible = true;
    },
    enterMessage(){
      this.charLeft = 200 - this.message.length
      if(this.message.length >= 200){
        this.message = this.message.slice(0,200)
        this.charLeft = 0
      }
    },
    async handleOk () {
        if(this.message.length < 30){
          return
        }
        //dispatch api
        this.AsyncLoading = true
        try {
           await this.$store.dispatch('appointment/cancelCarMax',{...this.appointment,comment:this.message})
           this.visible = false
           this.AsyncLoading = false
        } catch (e) {
           this.AsyncLoading = false
        }
       

    },
    handleCancel () {
        this.visible = false
        this.message = ''
        this.charLeft = 200
    },
    showStatusConfirm(status) {
      if(this.appointment.carMaxImgUrl){
        if(!this.appointment.quotePrice){
          return alert('Please quote!' )
        }
      }
      if(this.appointment.appointmentStatus === 'NO_SHOW'||this.appointment.appointmentStatus === 'PURCHASED' || this.appointment.appointmentStatus === 'CANCELED') return
      this.$modal.confirm({
        title: 'Alert',
        content: 'This action cannot be reversed',
        okText: 'CONFIRM',
        cancelText: 'Cancel',
        onOk: () => {if(status==='NO_SHOW'){
          this.radio1 = status
          this.radio2 = ''
          this.updateStatus(status)
          }else{
            this.radio2 = status
            this.radio1 = ''
            this.updateStatus(status)
          }},
        onCancel: function() {},
      })
    },
    quote(price) {
      this.$store.dispatch('appointment/quote', {appointmentId:this.$route.params.id,price,toMchId:this.$route.params.userId})
    },
    load(id, userId) {
      this.$store.dispatch('appointment/loadById', {id, userId})
    },
    goBack() {
      this.$router.go(-1)
    },
    updateStatus(status){
      this.$store.dispatch('appointment/updateStatus',{...this.appointment,appointmentStatus:status})
    },

  },
  filters: {
    dateFormat: function(v) {
      return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8) + ' ' + time12Format(v.slice(8, 12))
    },
    location:  function(v) {
      switch (v) {
        case 'Pasadena0': return 'Pasadena'
        case 'Burbank0': return 'Burbank'
        case 'Arcadia0': return 'Arcadia'
        case 'WoodlandHills0': return 'Woodland Hills'
        case 'LAX0': return 'LAX'
        case 'WestLosAngeles0': return 'West LosAngeles'
        case 'Irvine0': return 'Irvine'
        case 'CostaMesa0': return 'Costa Mesa'
        default: return 'Carmax'
      }
    },
  },
  mounted() {
  },
}
</script>

<style>
.vehicle-detail {
  width: 96%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.header-fd{
display: flex;
flex-wrap: wrap;
justify-content: space-between;
width: 700px
}
.appointment div.ant-tabs-bar {
  margin-bottom: 0;
  border-bottom-color: #f0f2f5;
}
.appointment div.ant-tabs-nav-scroll {
  padding: 0 2%;
  background-color: #fff;
}
.appointment div.ant-tabs-content {
  padding-top: 21px;
}
.vehicleInfoRight div .ant-input {
  border: 1px solid #fff;
}

@media screen and (max-width: 768px) {
.vehicle-detail {
  width: 96%;
  flex-direction: column;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
  .header-fd{
    width: 250px;
    flex-direction: column;
    justify-content: space-around;
  }
  .carDetailLeft,
  .carDetailright {
    width: 100%;
  }
}
</style>



