import Storage from 'sdk/hiveel-Storage'
import ParamUtil from 'sdk/hiveel-ParamUtil'
import Exception from 'sdk/hiveel-Exception'
export default class AjaxUtil {
  static run(url, options) {
    options.headers = new Headers(options.headers)
    if (options.body && options.body.constructor === Object) {
      options.body = ParamUtil.createUrlEncode(options.body)
      if (!options.headers.get('Content-Type')) {
        options.headers.append('Content-Type', 'application/x-www-form-urlencoded')
      }
    }
    options.headers.append('Authorization', `Bearer ${Storage.get('hiveel.token')}`)
    return fetch(url, options)
      .then(response => response.ok ? response.json() : Exception.createRest(response.status, response.statusText))
      .catch(error => Exception.createRest(null, error))
  }
}



