import PushSingleMemberService from 'sdk/hiveel-PushSingleMemberService'
import MemberService from 'sdk/hiveel-MemberService'

const CLEAN_STATE = {
  total: 0,
  page: {curr: 1, max: 0},
  filter: {name: '', start: 0, limit: 10},
  list: [],
}

const state = Object.assign({}, CLEAN_STATE)

const actions = {
  goPage({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('page', payload)
    dispatch('load')
  },
  reset({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('reset', 'REMOVE')
    dispatch('count')
    dispatch('load')
  },
  changeFilter({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('page', 1)
    commit('filter', state.filter)
    dispatch('count')
    dispatch('load')
  },
  async count({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await PushSingleMemberService.count(state.filter)
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    rest.data = 1000
    commit('total', rest.data)
  },
  async load({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await PushSingleMemberService.load(state.filter)
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    let memberIdList = [...new Set(rest.data.map(e => e.memberId))]
    let memberRest = await MemberService.loadByIdList(memberIdList.concat(0))
    let memberMap = {}
    memberRest.data.forEach(e => { memberMap[e.id] = e })
    rest.data.forEach(e => {
      e.member = memberMap[e.memberId]
    })
    commit('list', rest.data)
  },
}

const mutations = {
  page(state, e) {
    state.page.curr = e
    state.filter.start = (e - 1) * state.filter.limit
  },
  total(state, e) {
    state.total = e
    state.page.max = e / state.filter.limit
  },
  reset(state, e) {
    state.filter = Object.assign({}, CLEAN_STATE).filter
    state.list = []
  },
  filter(state, e) {
    state.filter = e
  },
  list(state, e) {
    state.list = e
  },
}

export default {
  namespaced: true, state, actions, mutations,
}



