<template>
  <main style="background-color:#fff; justify-content:center; align-items: center">

    <v-tabs active-tab-key="1" @tab-click="onTabClick">
      <v-tab-pane style="flex-direction: column; justify-content:center; align-items: center" class='center' tab-key="1" tab="West Covina" >
    <button class="btn-green" style="height: 32px; margin: 10px 0; width: 85px; background: #10b975;" v-on:click="getPdf()">Export PDF</button>
    <div class="row" :id="WCDOM" style="padding-top: 55px;background-color:#fff; width:840px">
    <div style="background-color:#FBFBFB;">
    <section class="center" style="background: #fff; display: flex; align-items: center; flex-direction: row; justify-content: space-between">
      <div style="width: 90%; margin: 26px auto;display: flex; justify-content: space-between;">
          <div style="width: 50%; display: flex; justify-content: space-between; flex-direction: column;">
                <div style="font-size: 20px; font-weight: 500;">Hiveel Technologies, Inc</div>
                <div style="margin-top:10px; font-size: 14px;color:#828691">1050 Lakes Drive Suite 210</div>
                <div style="font-size: 14px;color:#828691">West Covina, CA 91790</div>
                <div style="font-size: 18px; margin-top:40px">Trade-In Solutions</div>
          </div>
          <div style="width: 50%; display: flex; flex-direction: column; text-align: right">
                <div style="font-size: 32px; font-weight: 500;">INVOICE</div>
                <div style="margin-top:10px; font-size: 14px;">Invoice#&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">trade-in-{{location}}-{{this.$route.params.invoiceId}}</span></div>
                <div style="margin-top:10px; font-size: 14px;">Invoice Date&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">{{this.$route.params.dueDate|dateFormat}}</span></div>
                <div style="margin-top:10px; font-size: 14px;">Due Date&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">{{this.$route.params.dueDate|dateFormat2}}</span></div>
          </div>
      </div>
    </section>
    <section style = " width: 100%; background: #fff; display: flex; align-items: center; flex-direction: column">
    <dataTable style = "margin-top: 0; margin-bottom:0; width:95%">
      <dataTableTitle slot="title" :columnList="columnList"></dataTableTitle>
      <article slot="content" class="row" style="border-bottom: 1px solid #dfdfdf;" v-for="(e, i) in listWC" :key="i">
        <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
          <div :style="{flex: columnList[0].flex}">Service</div>
           <div :style="{flex: columnList[1].flex}">{{e.bizOrderNo}} - {{e.firstName}} {{e.lastName}}</div>
          <div :style="{flex: columnList[2].flex}">$30.00</div>
          <div :style="{flex: columnList[3].flex}">1</div>
          <div :style="{flex: columnList[4].flex}">$30.00</div>
        </section>
      </article>
    </dataTable>
          <section style="width: 90%; background: #fff; display: flex; align-items: center; flex-direction: row; justify-content:flex-end;margin-bottom:100px">
              <div style="width: 40%; flex-direction: column; ">
                <div style="margin: 0 auto; width:100%; height: 50px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;"> 
                    <div style="font-weight: bold; font-size:14">WC Amount</div>
                    <div style="font-size:14">${{30*listWC.length|number}}</div>
                </div>
                <div style="margin: 0 auto; width:100%; height: 50px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;  border-bottom: 2px solid #E7E8EC;"> 
                    <div style="font-weight: bold; font-size:14">LA Amount</div>
                    <div style="font-size:14">${{this.$route.params.totalAmount - 30*listWC.length|number}}</div>
                </div>
                <div style="margin: 0 auto; width:100%; height: 50px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;"> 
                    <div style="font-weight: bold;">Total Amount</div>
                    <div style="font-size:32px;font-weight:bold"> ${{this.$route.params.totalAmount|number}}</div>
                </div> 
              </div>
      </section>
    </section>
    </div>
    </div>
  </v-tab-pane>

<v-tab-pane style="flex-direction: column; justify-content:center; align-items: center" class='center' tab-key="2" tab="West Los Angeles" >
    <button class="btn-green" style="height: 32px; margin: 10px 0; width: 85px; background: #10b975" v-on:click="getPdf()">Export PDF</button>
    <div class="row" :id="LADOM" style="padding-top: 55px;background-color:#fff; width:840px">
    <div style="background-color:#FBFBFB;">
    <section class="center" style="background: #fff; display: flex; align-items: center; flex-direction: row; justify-content: space-between">
      <div style="width: 90%; margin: 26px auto;display: flex; justify-content: space-between;">
          <div style="width: 50%; display: flex; justify-content: space-between; flex-direction: column;">
                <div style="font-size: 20px; font-weight: 500;">Hiveel Technologies, Inc</div>
                <div style="margin-top:10px; font-size: 14px;color:#828691">1050 Lakes Drive Suite 210</div>
                <div style="font-size: 14px;color:#828691">West Covina, CA 91790</div>
                <div style="font-size: 18px; margin-top:40px">Trade-In Solutions</div>
          </div>
          <div style="width: 50%; display: flex; flex-direction: column; text-align: right">
                <div style="font-size: 32px; font-weight: 500;">INVOICE</div>
                <div style="margin-top:10px; font-size: 14px;">Invoice#&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">trade-in-{{location}}-{{this.$route.params.invoiceId}}</span></div>
                <div style="margin-top:10px; font-size: 14px;">Invoice Date&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">{{this.$route.params.dueDate|dateFormat}}</span></div>
                <div style="margin-top:10px; font-size: 14px;">Due Date&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span style="color:#828691">{{this.$route.params.dueDate|dateFormat2}}</span></div>
          </div>
      </div>
    </section>
    <section style = " width: 100%; background: #fff; display: flex; align-items: center; flex-direction: column">
    <dataTable style = "margin-top: 0; margin-bottom:0; width:95%">
      <dataTableTitle slot="title" :columnList="columnList"></dataTableTitle>
      <article slot="content" class="row" style="border-bottom: 1px solid #dfdfdf;" v-for="(e, i) in listLA" :key="i">
        <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
          <div :style="{flex: columnList[0].flex}">Service</div>
           <div :style="{flex: columnList[1].flex}">{{e.bizOrderNo}} - {{e.firstName}} {{e.lastName}}</div>
          <div :style="{flex: columnList[2].flex}">$30.00</div>
          <div :style="{flex: columnList[3].flex}">1</div>
          <div :style="{flex: columnList[4].flex}">$30.00</div>
        </section>
      </article>
    </dataTable>
          <section style="width: 90%; background: #fff; display: flex; align-items: center; flex-direction: row; justify-content:flex-end;margin-bottom:100px">
              <div style="width: 40%; flex-direction: column; ">
                <div style="margin: 0 auto; width:100%; height: 50px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;"> 
                    <div style="font-weight: bold; font-size:14">LA Amount</div>
                    <div style="font-size:14">${{30*listLA.length|number}}</div>
                </div>
                <div style="margin: 0 auto; width:100%; height: 50px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;  border-bottom: 2px solid #E7E8EC;"> 
                    <div style="font-weight: bold; font-size:14">WC Amount</div>
                    <div style="font-size:14">${{this.$route.params.totalAmount - 30*listLA.length|number}}</div>
                </div>
                <div style="margin: 0 auto; width:100%; height: 50px; display: flex; align-items: center; flex-direction: row; justify-content:space-between;"> 
                    <div style="font-weight: bold;">Total Amount</div>
                    <div style="font-size:32px;font-weight:bold"> ${{this.$route.params.totalAmount|number}}</div>
                </div> 
              </div>
      </section>
    </section>
    </div>
    </div>
            </v-tab-pane>   
  </v-tabs>
  </main>
</template>
<script>
import dataTable from 'components/dataTable'
import dataTableTitle from 'components/dataTable/title'
import dataTablePage from 'components/dataTable/page'
import {dateFormat, time12Format} from '../../assets/String.js'
export default {
  data() {
    return {
      location:'WC',
      WCDOM:'pdfDom',
      LADOM:'',
      htmlTitle:  'trade-in-WC-'+this.$route.params.invoiceId,
      columnList: [
        {name: 'ITEM', sortBy: 'id', flex: 1},
        {name: 'DESCRIPTION', sortBy:'',flex:3},
        {name: 'UNIT PRICE', sortBy: '', flex: 1},
        {name: 'QUANTITY', sortBy: '', flex: 1},
        {name: 'AMOUNT', sortBy: '', flex: 1},
      ],
      goPage: (i) => {
        this.$store.dispatch('financeService/goPage', i)
      },
    }
  },
  computed: {
    total() { return this.$store.state.financeService.total },
    page() { return this.$store.state.financeService.page },
    list() { return this.$store.state.financeService.detail},
    listWC() { return this.$store.state.financeService.detailWC},
    listLA() { return this.$store.state.financeService.detailLA},
  },
  filters: {
    dateFormat: function(v) {
      return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8)
    },
    dateFormat2: function(v) {
        let day = ''
      if(v.slice(2, 4) === '01'){
          day = '08'
      }else{
          day = '23'
      }
      return v.slice(0, 2) + '-' + day + '-' + v.slice(4, 8)
    },
    storeName: function(v) {
      return v === 'store001' ? 'WC' : v === 'store002' ? 'LA' : 'On site'
    },
  },
  components: {
    dataTable, dataTableTitle, dataTablePage,
  },
  created() {
    this.loadById(this.$route.params.id)
  },
  mounted() {
  },
  methods: {
    onTabClick(tabKey) {
      this.storeId = tabKey === '1' ? 'store001' : 'store002'
      this.location = tabKey === '1' ? 'WC' : 'LA'
      this.htmlTitle = tabKey === '1' ?  'trade-in-WC-'+this.$route.params.invoiceId :  'trade-in-LA-'+this.$route.params.invoiceId
      this.WCDOM = tabKey === '1' ? 'pdfDom' : ''
      this.LADOM = tabKey === '1' ? '' : 'pdfDom'
    },
    loadById(e) {
      this.$store.dispatch('financeService/loadById', e)
    },
  },
}
</script>
<style scoped>
input[type=radio]:checked ~ div {
  /* color: #4A90E2; */
  background-color:#F1F1F1;
}
.ant-radio-button-wrapper-checked{
  background-color: #ff0;
}
.row {
  cursor: pointer;
}
.row:hover {
  background: #e8f4fd;
  /* color: #fff; */
}
</style>



