import AccountManagementService from 'sdk/hiveel-AccountManagementService'
const CLEAN_STATE = {
  list: [],
  total: 0,
  page: {curr: 1, max: 0},
}
const state = Object.assign({}, CLEAN_STATE)
const actions = {
  reset({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('reset', payload)
    dispatch('load')
  },
  search({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('search', payload)
  },
  async load({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AccountManagementService.load()
    if (rest.code !== 'SUCCESS') {
      return
    }
    commit('list', rest.data.filter(e => e.accountPermission !== 'disable'))
  },
  async register({commit, dispatch, state, rootState}, payload, config = {}) {
    payload.permission = rootState.me.permission
    let rest = await AccountManagementService.registerReadOnly(payload)
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    dispatch('load')
  },
  async delete({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await AccountManagementService.delete(payload)
    if (rest.code !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    dispatch('load')
  },
}

const mutations = {
  search(state, e) {
    if (e) {
      state.list = state.list.filter(function(item) {
        return item.userName === e
      })
    }
  },
  total(state, e) {
    state.total = e
  },
  reset(state, e) {
    state.list = []
  },
  list(state, e) {
    state.list = e
    state.total = e.length
  },
}

export default {
  namespaced: true, state, actions, mutations,
}



