
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
// import Hiveel from 'sdk/hiveel-Hiveel'
import Config from 'sdk/hiveel-Config'
import ParamUtil from 'sdk/hiveel-ParamUtil'

export default class {
  static me() {
    let url = Config.authServer + '/auth/me'
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }

  static updatePhoneCode(member) {
    let url = Config.authServer + '/auth/verifyPhoneNumber'
    let formData = new FormData()
    formData.append('toNumber', member.phoneNumber)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }

  static updatePhone(member) {
    let url = Config.authServer + '/auth/updateVerifiedNumber'
    let formData = new FormData()
    formData.append('phoneNumber', member.phoneNumber)
    formData.append('code', member.code)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }

  static updateEmailCode(member) {
    let url = Config.authServer + '/auth/verifyEmail'
    let formData = new FormData()
    formData.append('email_to', member.email)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }

  static updateEmail(member) {
    let url = Config.authServer + '/auth/updateVerifiedEmail'
    let options = {
      method: 'GET',
      body: member,
    }
    return AjaxUtil.run(url, options)
  }

  static updateNickName(member) {
    let url = Config.authServer + '/auth/updateNames'
    let formData = new FormData()
    formData.append('nickName', member.nickName)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }
  static updateAvatar(member) {
    let url = Config.authServer + '/auth/updateImageUrl'
    let options = {
      method: 'POST',
      body: member,
    }
    return AjaxUtil.run(url, options)
  }

  static updateShowPhone(member) {
    let url = Config.authServer + '/auth/updateShowPhone'
    let options = {
      method: 'POST',
      body: member,
    }
    return AjaxUtil.run(url, options)
  }

  static updatePassword(member) {
    let url = Config.authServer + '/auth/changePassword'
    let formData = new FormData()
    formData.append('password', member.password)
    formData.append('oldPassword', member.oldPassword)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }

  static loadByIdList(list) {
    let url = Config.authServer + '/auth/getUsers?ids=' + list.toString()

    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
  static load(filter, page) {
    filter.size = filter.limit
    filter.page = page.curr
    let url = Config.authServer + '/auth/users/member?' + ParamUtil.createUrlEncode(filter)
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
}



