<template>
  <div style="height: 60px; display: flex; justify-content: space-between; align-items: center;">
    <div v-if="total!=0" @click="exportCSV(1000)" style = "cursor: pointer">
      <v-icon type="download" style="margin-right: 5px; font-size: 16px; color: #10B975"></v-icon>
      Download this form
    </div>
    <div style = "display: flex; justify-content: flex-end; align-items: center;">
      <div class="center f-t2" style="margin: 0 5px; ">Total: {{total}} {{name}}</div>
      <button class="center btn-white" style="margin: 0 5px; width: 30px; height: 30px; border-radius: 6px; " v-if="page.curr-1>0&&name!='record(s)'" @click="goPage(page.curr-1)"><v-icon type="left"></v-icon></button>
      <button class="center btn-white" style="margin: 0 5px; width: 30px; height: 30px; border-radius: 6px; " v-for="i in 4" :key="'p'+(page.curr-i)" v-if="page.curr-5+i>0&&name!='record(s)'" @click="goPage(page.curr-5+i)">{{page.curr-5+i}}</button>
      <button class="center btn-green" style="margin: 0 5px; width: 30px; height: 30px; border-radius: 6px; " v-if="total!=0&&name!='record(s)'">{{page.curr}}</button>
      <button class="center btn-white" style="margin: 0 5px; width: 30px; height: 30px; border-radius: 6px; " v-for="i in 4" :key="'p'+(page.curr+i)" v-if="page.curr+i<page.max&&name!='record(s)'" @click="goPage(page.curr+i)">{{page.curr+i}}</button>
      <button class="center btn-white" style="margin: 0 5px; width: 30px; height: 30px; border-radius: 6px; " v-if="page.max>=2&&name!='record(s)'" @click="goPage(page.curr+1)"><v-icon type="right"></v-icon></button>
  </div>

  </div>
</template>
<script>
export default {
  props: ['name', 'total', 'page', 'goPage', 'exportCSV'],
  data() {
    return {
    }
  },
  computed: {
  },
}
</script>
<style scoped>
</style>



