// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import 'vue-beauty/package/style/vue-beauty.min.css'
import '@/assets/css/global.css'

import hiveelPlugin from 'sdk/hiveel-plugin'
import Vue from 'vue'
import DatePicker from 'vue2-datepicker'
import store from '@/store'
import vueBeauty from 'vue-beauty'
import number from './filter/number'
import phone from './filter/phone'
import date from './filter/date'
import App from '@/App'
import router from '@/router'
import {apptStatus} from './assets/String'
import htmlToPdf from './components/utils/htmlToPdf'
Vue.use(htmlToPdf)
Vue.use(DatePicker)
Vue.use(vueBeauty)
Vue.use(hiveelPlugin)
Vue.use(htmlToPdf)
Vue.config.productionTip = false

Vue.filter('number', number)
Vue.filter('phone', phone)
Vue.filter('date', date)
Vue.filter('apptStatus', apptStatus)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
})



