<template>
        <section style="display: flex; align-items: center; justify-content: space-between; background-color: #ececec">
            <div class="appointment">
                <div class="calendar-container">
                    <div :style="schedule.length !== 0 ? 'margin-top: 80px;height: 400px; display: flex; flex-direction: column; align-items: center;' : 'margin-top: 50px;height: 400px; display: flex; flex-direction: column; align-items: center;'">
                        <div style="width: 420px;">
                            <div id='info' style='width: 100%; display: flex; justify-content: space-between;'>
                                <div @click='previousM' style="cursor: pointer;">
                                    <img src='../../../assets/img/calLeft.png' alt='' style="width: 15px; height: 15px;">
                                </div>
                                <div>
                                    <span style="margin-right: 10px; font-size: 18px;">{{months[tableMonth]}}</span>
                                    <span>{{tableYear}}</span>
                                </div>
                                <div @click='nextM' style="cursor: pointer;">
                                    <img src='../../../assets/img/calRight.png' alt='' style="width: 15px; height: 15px;">
                                </div>
                            </div>

                            <div class='calendar-head'>
                                <div class="head-group">Sun</div>
                                <div class="head-group">Mon</div>
                                <div class="head-group">Tue</div>
                                <div class="head-group">Wen</div>
                                <div class="head-group">Thu</div>
                                <div class="head-group">Fri</div>
                                <div class="head-group">Sat</div>
                            </div>

                            <div class="calendar">
                                <div v-for="e in firstWeekDay"  :key="e + tableMonth"  style="margin-right: -1px; margin-bottom: -1px;width: 61px; height: 45px; border: 1px solid #BFBFBF; "></div>
                                <div
                                    class="calendar-cell"
                                    v-for="(a) in (tableMonth===1 ? leapFeb :monDays[tableMonth].duration)"
                                    :key="a + tableYear"
                                    style="margin-right: -1px; margin-bottom: -1px;width: 61px; height: 45px; border: 1px solid #BFBFBF; display: flex; align-items: center;justify-content: space-around; cursor: pointer;"
                                    @click="getDate(a)"
                                    :style="{
                                        background: monDays[tableMonth].active.indexOf(a) >= 0 ?'#3E8EE6' :(((tableYear < today_year) || (tableYear === today_year && today_month > tableMonth) || (tableMonth === today_month && today_day > a)) ? '#ececec' : 'transparent'),
                                        color: monDays[tableMonth].active.indexOf(a) >= 0 ?'#fff' :(((tableYear < today_year) || (tableYear === today_year && today_month > tableMonth) || (tableMonth === today_month && today_day > a)) ? '#fff' : '#444')
                                    }"
                                >
                                    {{a}}
                                </div>
                                <div v-for="b in emptyCell" :key="b*100" style="margin-right: -1px; margin-bottom: -1px;width: 61px; height: 45px; border: 1px solid #BFBFBF;"></div>
                            </div>
                        </div>
                    </div>
                </div>

                <div v-if="schedule.length !== 0" class="date" style="position:relative;margin-bottom:20px; flex-wrap: wrap-reverse; justify-content: space-between">
                    <div v-if="!editable && schedule.length !== 0"  style = "position:absolute; right:20px; top:20px; z-index: 999">
                        <div @click="editBt" class="center" style = "cursor: pointer;  height: 30px; width: 90px; background-color: #4A90E2; color: #fff; font-size:14; font-weight: 500; border-radius: 4px; ">
                        Edit
                        </div>
                    </div>
                    <div v-if="editable && schedule.length !== 0" style="position:absolute; right:20px; top:20px; display: flex; flex: 1; z-index: 999">
                      <div class="center" @click="update" style= "cursor: pointer; margin-right: 20px; height: 30px; width: 90px; background-color: red; color: #fff; font-size:14; font-weight: 500; border-radius: 4px; ">Save</div>
                      <div class="center" @click="editBt" style= "cursor: pointer; height: 30px; width: 90px; background-color: #4A90E2; color: #fff; font-size:14; font-weight: 500; border-radius: 4px; ">Cancel</div>
                    </div>
                    <div style = "flex: 3; display: flex; margin-top:30px">
                        <div style="flex: 1;">
                            <div style="margin-top: 50px; display: flex; flex-direction: column; align-items: center; ">
                                <div style="font-size: 18px;">Morning</div>
                                <div style="margin-top: 20px;">
                                    <div class="center" style="cursor:pointer; margin-bottom: 15px; width: 124px; height: 40px; border: 1px solid #BFBFBF;"

                                    v-for="(e, i) in schedule.slice(0,scheduleRange[0])" :key="'schedule'+i"
                                    :style="{
                                            background: schedule[i].appt ? '#10B975': schedule[i].status?'#E1E1E1':'#FFFFFF',
                                            color: schedule[i].appt ? '#FFFFFF': '#000000',
                                    }"
                                    @click="changeBlockTime(schedule[i],i)">{{schedule[i].value | timeFormat}}</div>
                                </div>
                            </div>
                        </div>
                        <div style="flex: 1;">
                            <div style="margin-top: 50px; display: flex; flex-direction: column; align-items: center; ">
                                <div style="font-size: 18px;">Afternoon</div>
                                <div style="margin-top: 20px;">
                                    <div class="center" style="cursor:pointer; margin-bottom: 15px; width: 124px; height: 40px; border: 1px solid #BFBFBF;"
                                    v-for="(e, i) in schedule.slice(scheduleRange[0],scheduleRange[1])" :key="'schedule'+i+scheduleRange[0]"
                                    :style="{
                                            background: schedule[i+scheduleRange[0]].appt ? '#10B975': schedule[i+scheduleRange[0]].status?'#E1E1E1':'#FFFFFF',
                                            color: schedule[i+scheduleRange[0]].appt ? '#FFFFFF': '#000000',
                                    }"
                                    @click="changeBlockTime(schedule[i+scheduleRange[0]],i+scheduleRange[0])"
                                    >{{schedule[i+scheduleRange[0]].value | timeFormat }}</div>
                                </div>
                            </div>
                        </div>
                        <div style="flex: 1;">
                            <div style="margin-top: 50px; display: flex; flex-direction: column; align-items: center; ">
                                <div style="font-size: 18px;">Evening</div>
                                <div style="margin-top: 20px;">
                                    <div class="center" style="cursor:pointer; margin-bottom: 15px; width: 124px; height: 40px; border: 1px solid #BFBFBF;"
                                    v-for="(e, i) in schedule.slice(scheduleRange[1])" :key="'schedule'+i+scheduleRange[1]"
                                    :style="{
                                            background: schedule[i+scheduleRange[1]].appt ? '#10B975': schedule[i+scheduleRange[1]].status?'#E1E1E1':'#FFFFFF',
                                            color: schedule[i+scheduleRange[1]].appt ? '#FFFFFF': '#000000',
                                    }"
                                    @click="changeBlockTime(schedule[i+scheduleRange[1]],i+scheduleRange[1])"
                                    >{{schedule[i+scheduleRange[1]].value | timeFormat}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
</template>
<script>
import {fourDigitsTime} from '../../../assets/String.js'
import timeList from '../../../assets/timeList.js'
export default {
      props: [
    'userId',
    'storeId'
  ],
  data() {
    return {
      chosenDay: 0,
      scheduleRange: [0, 0],
      businessType: 'TRADE_IN',
      blockTime: [],
      appTime: [],
      editable: false,
      months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      leapFeb: 28,
      today: '',
      today_year: '',
      today_month: '',
      today_day: '',
      tableDay: '',
      tableYear: '',
      tableMonth: '',
      firstDay: '',
      firstDayDate: '',
      firstWeekDay: '',
      firstSunday: '',
      active: false,
      tempSchedule:[],
      monDays: [
        {duration: 31, active: []},
        {duration: 28, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
      ],
    }
  },
  filters: {
    timeFormat: function(v) {
      if (!v) return ''
      let h = v.slice(0, 2)
      let m = v.slice(2, 4)
      let category = 'AM'
      if (parseInt(h) === 12) {
        category = 'PM'
      } else if (parseInt(h) === 24) {
        h = 0
        category = 'AM'
      } else if (parseInt(h) > 12) {
        h = parseInt(h) - 12
        category = 'PM'
      } else {
        category = 'AM'
      }
      h = ('0' + h).slice(-2)
      return h + ':' + m + ' ' + category
    },
    storeLocation: function(v) {
      switch (v) {
        case 'Pasadena0': return 'Pasadena'
        case 'Burbank0': return 'Burbank'
        case 'Arcadia0': return 'Arcadia'
        case 'WoodlandHills0': return 'Woodland Hills'
        case 'LAX0': return 'LAX'
        case 'WestLosAngeles0': return 'West LosAngeles'
        case 'Irvine0': return 'Irvine'
        case 'CostaMesa0': return 'Costa Mesa'
        case 'CarmaxVirtual0':return 'Carmax'
        default: return v
      }
    },
  },
  methods: {
    saveTime(time) {
      if (time.length === 0) {
        if (this.blockT.length !== 0) {
          this.$store.dispatch('calendar/delete', {
            storeId: this.storeId,
            businessType: this.businessType,
            dateStr: this.digitString(this.tableMonth + 1) + this.digitString(this.tableDay) + this.tableYear,
            times: this.blockT.toString(),
            accountId: this.userId,
          })
        }
      } else {
        this.$store.dispatch('calendar/update', {
          businessType: this.businessType,
          blockTime: time,
          storeId: this.storeId,
          dateStr: this.digitString(this.tableMonth + 1) + this.digitString(this.tableDay) + this.tableYear,
          userId: this.userId,
        })
      }
    },
    changeBlockTime(e, i) {
      if (!this.editable) return
      if (e.appt) return
      let index = this.blockTime.indexOf(e.value)
      if (index > -1) {
        this.blockTime.splice(index, 1)
        this.tempSchedule[i].status = false
      } else {
        this.blockTime.push(e.value)
        this.tempSchedule[i].status = true
      }
    },
    getBlockTime(month, day, year) {
      this.$store.dispatch('calendar/loadBlockTime', {
        month: this.digitString(month),
        day: this.digitString(day),
        year: this.digitString(year),
        type: this.businessType,
        storeId: this.storeId,
        userId: this.userId,
        // storeId: this.storeId,
      })
    },
    digitString(v) {
      if (parseInt(v) < 10) {
        return '0' + v
      }
      return ''+v
    },
    update() {
      this.editable = !this.editable
      this.saveTime(this.blockTime)
    },
    editBt() {
      if (this.editable) {
        this.editable = !this.editable
        this.getBlockTime(this.tableMonth + 1, this.tableDay, this.tableYear)
      } else {
        this.blockTime = [...this.blockT]
        this.editable = !this.editable
      }
    },
    getCal() {
      this.tableMonth = this.today_month
      this.tableYear = this.today_year
    },
    tableCreate() {
      this.firstDay = new Date(this.tableYear, this.tableMonth, 1)
      if (this.tableYear % 4 === 0) {
        this.leapFeb = 29
      } else {
        this.leapFeb = 28
      };
      this.firstDayDate = this.firstDay.getDate()// 1
      this.firstWeekDay = this.firstDay.getDay()// 4
      if (this.firstWeekDay === 0) {
        this.firstSunday = this.firstDayDate
      } else {
        this.firstSunday = (7 - this.firstWeekDay) + this.firstDayDate
      }
    },

    nextM() {
      this.tableMonth++
      if (this.tableMonth === 12) {
        this.tableYear++
        this.tableMonth = 0
        this.tableCreate()
      } else {
        this.tableCreate()
      };
    },

    previousM() {
      this.tableMonth--
      if (this.tableMonth < 0) {
        this.tableYear--
        this.tableMonth = 11
        this.tableCreate()
      } else {
        this.tableCreate()
      }
    },
    getDate(e) {
      if (this.tableYear < this.today_year) return
      if (this.tableYear === this.today_year && this.today_month > this.tableMonth) return
      if (this.tableMonth === this.today_month && this.today_day > e) return
      // if ((e - this.firstSunday) % 7 === 0) return
      this.monDays = [
        {duration: 31, active: []},
        {duration: 28, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
      ]
      let length = this.tempSchedule.length
      let datechose = new Date(this.tableYear, this.tableMonth, e)
      this.chosenDay = datechose.getDay()
      this.tableDay = e
      this.monDays[this.tableMonth].active.push(e)
      this.getBlockTime(this.tableMonth + 1, e, this.tableYear)
    },
    pageBegin() {
      this.today = new Date()
      this.today_year = this.today.getFullYear()
      this.today_month = this.today.getMonth()
      this.today_day = this.today.getDate()
      this.tableYear = this.today_year
      this.firstDay = new Date(this.today_year, this.today_month, 1)
      this.firstDayDate = this.firstDay.getDate()
      this.firstWeekDay = this.firstDay.getDay()
      if (this.firstWeekDay === 0) {
        this.firstSunday = this.firstDayDate
      } else {
        this.firstSunday = (7 - this.firstWeekDay) + this.firstDayDate
      }
      if (this.today_year % 4 === 0) {
        this.leapFeb = 29
      } else {
        this.leapFeb = 28
      }
    },

  },
  created() {
    this.pageBegin()
    this.getCal()
    this.getDate(this.today_day)
  },
  watch:{
    storeId: function(){
      this.getDate(this.tableDay)
    }
  },
  computed: {
    schedule() {
      let today = new Date()
      let curTime = (today.getHours() + 1) * 100 + today.getMinutes()
      let chosenDay = this.chosenDay||this.chosenDay === 0 ? this.chosenDay : today.getDay()
      let schedules = timeList.getTimeList(this.storeId)[chosenDay]
      let status = this.tableDay === this.today_day && this.tableMonth === this.today_month && this.today_year === this.tableYear
      let tempSchedule = []
      schedules.length!==0&&schedules.forEach((e,i) => {
        e = fourDigitsTime(e)
        tempSchedule[i] = new Object()
        tempSchedule[i].value = e
        tempSchedule[i].number = parseInt(e)
        if(tempSchedule[i].number === 1200) this.scheduleRange[0] = i
        if(tempSchedule[i].number === 1700) this.scheduleRange[1] = i
        tempSchedule[i].appt = this.$store.state.calendar.appointmentT.indexOf(e) !== -1
        tempSchedule[i].status = ((status && parseInt(e) <= curTime) || (this.$store.state.calendar.blockT.indexOf(e) !== -1 && !e.appt))
      })

      if(schedules.length!==0&&this.scheduleRange[0]!== 0 && this.scheduleRange[1]===0){
          this.scheduleRange[1]=schedules.length
      }
      this.tempSchedule = tempSchedule
      return tempSchedule
    },
    blockT() { return this.$store.state.calendar.blockT },
    emptyCell() {
      let empty = 7 - (this.firstWeekDay + this.monDays[this.tableMonth].duration) % 7
      return empty !== 7 ? empty : 0
    },
    accountPermission() { return this.$store.state.me.accountPermission },
  },
}
</script>

<style scoped>
.calendar-head {
    display: flex;
    width: 100%;
    text-align:center;
    margin-top: 20px;
}
.head-group {
    width: 70px;
    text-align: center;
    color: #BFBFBF;
}

.calendar {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    max-height: 300px;
    text-align:center;
    margin-top: 20px;
}
.calendar-cell {
  transition: all 0.2s ease-in;
}
.calendar-container {
    flex: 2
}
.date {
    flex: 2;
    display: flex;
}
.appointment {
    margin: 26px auto;
    width: 96%;
    background: #fff;
    display: flex;
}
@media (max-width: 1250px) {
    .appointment {
        flex-direction: column;
        align-items: center;
    }
    .calendar-container {
        flex: 1;
        width: 100%;
    }
    .date {
        flex: 1;
        width: 100%;
    }
}
</style>



