export default class Config {
  static get authServer() {
    return this._authServer
  }
  static set authServer(value) {
    this._authServer = value
  }
  static get sitebase() {
    return this._sitebase
  }
  static set sitebase(value) {
    this._sitebase = value
  }
  static get serviceServer() {
    return this._serviceServer
  }
  static set serviceServer(value) {
    this._serviceServer = value
  }
  static get refreshTime() {
    return this._refreshTime
  }
  static set refreshTime(value) {
    this._refreshTime = value
  }
}
Config.authServer = 'https://authserver.hiveel.com'
// Config.authServer = 'http://www.hiveel.co:5555'
Config.sitebase = 'https://apiserver.hiveel.com'
// Config.sitebase = 'http://www.hiveel.co:5500'
Config.refreshTime = 1000 * 60 * 4
