'use strict'

export function apptStatus(v) {
  if (v === 'SAVED') {
    return 'Booked'
  } else if(v === 'NO_SHOW'){
    return 'No Show'
  } else if(v === 'PURCHASED'){
    return 'Purchased'
  }else if (v === 'INVALID') {
    return 'Canceled'
  } else if (v === 'VALID') {
    return 'Booked'
  } else if (v === 'CANCELED') {
    return 'Canceled'
  } else if (v === 'CANCEL_AND_REFUNDED') {
    return 'Canceled'
  } else {
    return 'Completed' // SERVICE_DONE
  }
}
export function fourDigitsTime(v){
  if(!v) return;
  v = v.replace(':','')
  return v.length < 4 ?'0'+v:v

}
export function dateFormat(v) {
  return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8) + ' ' + time12Format(v.slice(8, 12))
}

export function formatDateStr(v) {
  return v.slice(5, 7) + v.slice(8, 10) + v.slice(0, 4)
}

export function time12Format(v) {
  if (!v) return ''
  let h = v.slice(0, 2)
  let m = v.slice(2, 4)
  let category = 'AM'
  if (parseInt(h) === 12) {
    category = 'PM'
  } else if (parseInt(h) === 24) {
    h = 0
    category = 'AM'
  } else if (parseInt(h) > 12) {
    h = parseInt(h) - 12
    category = 'PM'
  } else {
    category = 'AM'
  }
  h = ('0' + h).slice(-2)
  return h + ':' + m + ' ' + category
}
export function toLocal(utc) {
  return utc.indexOf('Z') >= 0 ? new Date(utc) : new Date(utc + 'Z')
}
export function toUtc(local) {
  return local.constructor === Date ? local.toISOString() : new Date(local).toISOString()
}



