<template>
  <div class="bg-grey-white" style="border-bottom: 1px solid #dfdfdf;">
    <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; ">
      <div class="f-t2 column" :class="{smallHidden: e.smallHidden}" style="align-items: center;" :style="{flex: columnList[i].flex}" v-for="(e, i) in columnList" :key="'column'+i" @click="changeSort(columnList[i].sortBy)">
        {{columnList[i].name}}
        <div v-if="columnList[i].sortBy" class="c-grey" style="width: 20px; display: flex; flex-direction: column; justify-content: center; align-items: center;">
          <div style="height: 5px;">
            <input type="radio" class="rdo-sort" v-model="sort" :value="columnList[i].sortBy + true"><i class="anticon anticon-caret-up" style="display: block; font-size: 6px;"></i>
          </div>
          <div style="height: 5px;">
            <input type="radio" class="rdo-sort" v-model="sort" :value="columnList[i].sortBy + false"><i class="anticon anticon-caret-down" style="display: block; font-size: 6px;"></i>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  props: ['columnList', 'changeSortBy'],
  data() {
    return {
      sort: '',
    }
  },
  computed: {
  },
  methods: {
    changeSort(v) {
      if (!v) {
        return
      }
      this.sort = this.sort === `${v}false` ? `${v}true` : `${v}false`
      this.changeSortBy(v)
    },
  },
}
</script>
<style scoped>
.rdo-sort {
  display: none;
}
.rdo-sort:checked ~ i {
  color: #4A90E2
}

</style>
<style>
.column {
  display: flex;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media screen and (max-width: 768px) {
  .smallHidden {
    display: none;
  }
}
</style>



