
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
// import ParamUtil from 'sdk/hiveel-ParamUtil'
import Config from 'sdk/hiveel-Config'
export default class {
  static load(e, page) {
    let url = Config.sitebase + '/hiveelpay/api/biz/merchant/invoice/search'
    // let url = Config.sitebase + '/hiveelpay/api/biz/merchant/invoice/search?' + ParamUtil.createUrlEncode(page)
    let formData = new FormData()
    if (e.curr) formData.append('currentPage', e.curr)
    if (e.total) formData.append('total', e.curr !== 1 ? e.total : 0)
    formData.append('perPageSize', e.limit)
    formData.append('keyWord', !e.keyWord ? '' : e.keyWord)
    formData.append('storeId', !e.storeId ? '' : e.storeId)
    formData.append('mchId', e.userId)
    formData.append('appointmentStatus', !e.appointmentStatus ? '' : e.appointmentStatus)
    formData.append('businessType', !e.type ? '' : e.type)
    formData.append('startDateStr', !e.startDateStr ? '' : e.startDateStr)
    formData.append('endDateStr', !e.endDateStr ? '' : e.endDateStr)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }
  static loadById(id) {
    let url = Config.sitebase + '/hiveelpay/api/biz/merchant/invoice/detail/' + id
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
}



