<template>
  <main>
    <section class="center" style="height: 70px; background: #fff;">
      <div style="height: 75px; width: 96%; dispplay: flex; justify-content: space-between;">
          <div class="f-t1" style="margin-top:25px; font-weight: 500; font-size: 20px">Setting</div>
      </div>
    </section>
    <section style = "margin: 26px auto; width: 96%; background: #fff; display: flex; align-items: center; flex-direction: column">
    <div style="width: 100%; height:60px; padding-left: 26px; background: #fff; display: flex; align-items: center; border-bottom: 1px solid #E9E9E9; font-size:16px; font-weight:500; ">Manage Account</div>
      <div v-if="accountPermission !== 'Admin'" class="f-12" style ="height: 32px; margin: 35px 0">Your account is "Read Only"</div>
    <dataTable  v-if="accountPermission === 'Admin'" style = "margin-top: 0; width:100%">
      <div slot="search" style="margin-top:5px; width: 100%; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap;">
        <button class="btn-green" style="height: 32px; width: 85px; background: #10b975; margin-bottom:10px;"   @click="onSaveType">+ New</button>
        <section style="margin:0 10px; margin-bottom:10px; width: 450px; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap">
          <section style="width: 260px; display: flex; justify-content: space-between; align-items: center; ">
            <div style="width: 20%">Search:</div>
            <v-input size="lg" placeholder="Please Enter" v-model="searchInfo" style="width: 80%"/>
          </section>
          <button class="btn-green" style="height: 32px; margin: 10px 0; width: 85px; background: #10b975"  @click="search">Search</button>
          <button class="btn-white" style="height: 32px; margin: 10px 0; width: 85px;" @click="reset">Reset</button>
        </section>
      </div>
      <dataTableTitle slot="title" :columnList="columnList"></dataTableTitle>
      <article slot="content" class="row" style="border-bottom: 1px solid #dfdfdf; " v-for="(e, i) in list" :key="i">
        <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
          <div :style="{flex: columnList[0].flex}">{{e.id}}</div>
          <div :style="{flex: columnList[1].flex}">{{e.userName}}</div>
          <div :style="{flex: columnList[2].flex}">{{e.accountPermission}}</div>
          <!-- <div :style="{flex: columnList[3].flex}" style="color: red" @click="showConfirm(e.id)"> -->
            <!-- Delete
          </div> -->
        </section>
      </article>
      <dataTablePage slot="page" name="account(s)" :total="total" :page="page" :exportCSV="exportCSV"></dataTablePage>
    </dataTable>
    </section>
    <popupBox>
      <div slot="content" class="center" style="width: 50%; max-width: 500px; height: 300px; background: #fff; border-radius: 2px; box-shadow: #e8e8e8 0 2px 4px 0;">
        <div style="width: 90%; height: 90%; display: flex; flex-direction: column; justify-content: space-between; position: relative;">
          <section style="position: absolute; right: 0; cursor: pointer" @click="offSaveType"><v-icon type="close"></v-icon></section>
          <v-form style="width: 100%; flex: 1;" direction="horizontal" :model="form" :rules="rules" ref="form">
            <div style="width: 100%; height: 100%; display: flex; flex-direction: column; justify-content:space-around; align-items: center;">
              <section class="f-t1">Add New User</section>
              <div style = "width: 80%;  display: flex; flex-direction: column; justify-content:center;">
              <section style="width: 90%; display: flex;  justify-content: space-between;">
                <div class="center" style="height: 32px;" >Account Name:</div>
                <v-form-item style="width: 70%" prop="name" has-feedback>
                  <v-input placeholder = "phone number/email address" type="text" size="large" v-model="form.newAccount"></v-input>
                </v-form-item>
              </section>
              <section style="width: 90%; display: flex;  justify-content: space-between;">
                <div class="center"  style="height: 32px; text-align: right;" >Password:</div>
                <v-form-item style="width: 70%" prop="name" has-feedback>
                  <v-input type="text" size="large" v-model="form.newPassword" placeholder = ""></v-input>
                </v-form-item>
              </section>
              </div>
              <section style="width: 40%; display: flex; justify-content: space-between;">
                <button class="btn-white" style="width: 70px; height: 30px;" type="button" @click="offSaveType">Cancel</button>
                <button class="btn-green" style="width: 70px; height: 30px;" type="button" @click="register">Add</button>
              </section>
            </div>
          </v-form>
        </div>
      </div>
    </popupBox>
  </main>
</template>
<script>
import dataTable from 'components/dataTable'
import dataTableTitle from 'components/dataTable/title'
import dataTablePage from 'components/dataTable/page'
import popupBox from 'components/popupBox'
export default {
  data() {
    return {
      rules: {
        content: [
          { required: true, message: 'please input the content' },
        ],
      },
      form: {newAccount: '', newPassword: ''},
      searchInfo: '',
      rangeDate1: ['2018-01-01', '2018-12-31'],
      statusList: [{value: 'ACTIVE', label: 'Active'}],
      columnList: [
        {name: 'Account ID', sortBy: 'id', flex: 2},
        {name: 'User Name', sortBy: '', flex: 2},
        {name: 'Permission', sortBy: '', flex: 1},
        // {name: 'Action', sortBy: '', flex: 1},
      ],
    }
  },
  computed: {
    total() { return this.$store.state.accountManagement.total },
    list() { return this.$store.state.accountManagement.list },
    page() { return this.$store.state.accountManagement.page },
    accountPermission() {
      return this.$store.state.me.accountPermission
    },
  },
  components: {
    dataTable, dataTableTitle, dataTablePage, popupBox,
  },
  created() {
    this.load()
  },
  mounted() {
  },
  methods: {
    exportCSV(i) {
      let ctr
      let columnDelimiter = ','
      let lineDelimiter = '\n'
      let csvContent = 'data:text/csv;charset=utf-8,'
      csvContent += 'AccountId,User Name,Permission\n'
      let keys = ['id', 'userName', 'accountPermission']
      this.list.forEach(function(item) {
        ctr = 0
        keys.forEach(function(key) {
          if (ctr > 0) csvContent += columnDelimiter
          csvContent += item[key]
          ctr++
        })
        csvContent += lineDelimiter
      })
      let encodedUri = encodeURI(csvContent)
      let link = document.createElement('a')
      link.setAttribute('href', encodedUri)
      link.setAttribute('download', 'Hiveel-Account.csv')
      link.click()
    },
    showConfirm(id) {
      this.$modal.confirm({
        title: 'Alert',
        content: 'This action cannot be reversed. Press "Confirm" to delete this account',
        okText: 'CONFIRM',
        cancelText: 'Cancel',
        onOk: () => this.deleteById(id),
        onCancel: function() {},
      })
    },
    deleteById(id) {
      console.log('infucntion', id)
      this.$store.dispatch('accountManagement/delete', {id})
    },
    onSaveType() {
      this.$store.dispatch('popupBox/on')
    },
    offSaveType() {
      this.$store.dispatch('popupBox/off')
    },
    reset() {
      this.$store.dispatch('accountManagement/reset')
    },
    load() {
      this.$store.dispatch('accountManagement/load')
    },
    register() {
      this.$store.dispatch('accountManagement/register',
        {userName: this.form.newAccount, password: this.form.newPassword})
      this.$store.dispatch('popupBox/off')
    },
    search() {
      this.$store.dispatch('accountManagement/search', this.searchInfo)
    },
  },
}
</script>
<style scoped>
.row {
  cursor: pointer;
}
.row:hover {
  background: #e8f4fd;
  /* color: #fff; */
}
</style>



