
import FinanceService from 'sdk/hiveel-FinanceService'
const CLEAN_STATE = {
  total: 0,
  page: {curr: 1, max: 0},
  filter: {limit: 10},
  list: [],
  detail: [],
  detailWC:[],
  detailLA:[]
}

const state = Object.assign({}, CLEAN_STATE)
const actions = {
  setStoreType({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('filter', payload)
    dispatch('load')
  },
  goPage({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('page', payload)
    dispatch('load')
  },
  reset({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('reset', payload)
    dispatch('load')
  },
  changeFilter({commit, dispatch, state, rootState}, payload, config = {}) {
    commit('page', 1)
    commit('filter', {...state.filter, ...payload})
    dispatch('load')
  },
  async load({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await FinanceService.load({...state.filter, ...state.page, total: state.total})
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('list', rest.data.list)
    commit('total', rest.data.page.total)
  },
  async loadById({commit, dispatch, state, rootState}, payload, config = {}) {
    let rest = await FinanceService.loadById(payload)
    if (rest.msg !== 'SUCCESS') {
      dispatch('failBox/fail', rest, {root: true})
      return
    }
    commit('detail', rest.data)
  },
}

const mutations = {
  page(state, e) {
    state.page.curr = e
  },
  total(state, e) {
    state.total = e
    state.page.max = e / state.filter.limit + (e % state.filter.limit !== 0 ? 1 : 0)
  },
  reset(state, e) {
    state.total = 0
    state.filter = e
    state.list = []
  },
  filter(state, e) {
    state.filter = e
  },
  list(state, e) {
    state.list = e
  },
  detail(state, e) {
    state.detail = e
    state.detailWC = e.filter(v=>v.toStoreId === 'store001')
    state.detailLA = e.filter(v=>v.toStoreId === 'store002')
  },
}

export default {
  namespaced: true, state, actions, mutations,
}



