<template>
  <main>
    <section class="center" style="height: 70px; background: #fff;">
      <div style="height: auto; width: 96%; dispplay: flex; justify-content: space-between;">
        <div style="font-size: 20px; font-weight: 500;">Financial Center</div>
      </div>
    </section>
    <section style = "margin: 26px auto; width: 96%; background: #fff; display: flex; align-items: center; flex-direction: column">
    <dataTable style = "margin-top: 0; width:100%">
      <dataTableTitle slot="title" :columnList="columnList"></dataTableTitle>
      <article slot="content" class="row" style="border-bottom: 1px solid #dfdfdf;" v-for="(e, i) in list" :key="i">
        <section style="margin: 0 auto; width: 98%; height: 50px; display: flex; align-items: center; justify-content: space-between; ">
          <div :style="{flex: columnList[0].flex}">{{e.appointmentId}}</div>
           <div :style="{flex: columnList[1].flex}">{{!e.carMaxImgUrl?'Onsite':'Carmax'}}</div>
          <div :style="{flex: columnList[2].flex}">{{e.firstName}} {{e.lastName}}</div>
          <div :style="{flex: columnList[3].flex}">{{e.toStoreId|storeName}}</div>
          <div :style="{flex: columnList[4].flex}">$30</div>
          <div :style="{flex: columnList[5].flex}">{{e.appointmentDateTimeStr|dateFormat}}</div>
        </section>
      </article>
      <dataTablePage slot="page" name="" :total="list.length" :page="page" :goPage="goPage" :exportCSV="exportCSV"></dataTablePage>
    </dataTable>

    </section>
  </main>
</template>
<script>
import dataTable from 'components/dataTable'
import dataTableTitle from 'components/dataTable/title'
import dataTablePage from 'components/dataTable/page'
import {dateFormat, time12Format} from '../../assets/String.js'
export default {
  data() {
    return {
      columnList: [
        {name: 'Appointment ID', sortBy: 'id', flex: 1},
        {name: 'Type', sortBy:'',flex:1},
        {name: 'User Name', sortBy: '', flex: 1},
        {name: 'Location', sortBy: '', flex: 1},
        {name: 'Lead Fee', sortBy: '', flex: 1},
        {name: 'Appointment Time', sortBy: '', flex: 1},
      ],
      goPage: (i) => {
        this.$store.dispatch('financeService/goPage', i)
      },
    }
  },
  computed: {
    total() { return this.$store.state.financeService.total },
    page() { return this.$store.state.financeService.page },
    list() { return this.$store.state.financeService.detail },
  },
  filters: {
    dateFormat: function(v) {
      return v.slice(0, 2) + '-' + v.slice(2, 4) + '-' + v.slice(4, 8) + ' ' + time12Format(v.slice(8, 12))
    },
    storeName: function(v) {
      return v === 'store001' ? 'West Covina' : v === 'store002' ? 'West Los Angeles' : 'On site'
    },
  },
  components: {
    dataTable, dataTableTitle, dataTablePage,
  },
  created() {
    this.loadById(this.$route.params.id)
  },
  mounted() {
  },
  methods: {
    exportCSV(i) {
      let ctr
      let columnDelimiter = ','
      let lineDelimiter = '\n'
      let csvContent = 'data:text/csv;charset=utf-8,'
      csvContent += 'Appointment ID, Type, firstName, LastName, Location, Lead Fee, AppointmentTime\n'
      let keys = ['appointmentId', 'carMaxImgUrl','firstName', 'lastName', 'toStoreId', 'payAmount', 'appointmentDateTimeStr']
      this.list.forEach(function(item) {
        ctr = 0
        keys.forEach(function(key) {
          if (ctr > 0) csvContent += columnDelimiter
          if (key === 'payAmount') {
            csvContent += '$30'
          } else if (key === 'appointmentDateTimeStr' || key === 'lastUpdateAt') {
            csvContent += dateFormat(item[key])
          } else if (key === 'toStoreId') {
            csvContent += item[key] === 'store001' ? 'West Covina' : 'West Los Angeles'
          } else if( key === 'carMaxImgUrl'){
              csvContent += !item[key]  ? 'Onsite' : 'Carmax'
          } 
          else {
            csvContent += item[key]
          }
          ctr++
        })
        csvContent += lineDelimiter
      })
      csvContent += 'Amount'
      csvContent += columnDelimiter+columnDelimiter+columnDelimiter+columnDelimiter+columnDelimiter
      csvContent += '$'+parseInt(this.list.length)*30
      let encodedUri = encodeURI(csvContent)
      let link = document.createElement('a')
      link.setAttribute('href', encodedUri)
      link.setAttribute('download', 'InvoiceDetail.csv')
      link.click()
    },
    loadById(e) {
      this.$store.dispatch('financeService/loadById', e)
    },
  },
}
</script>
<style scoped>
input[type=radio]:checked ~ div {
  /* color: #4A90E2; */
  background-color:#F1F1F1;
}
.ant-radio-button-wrapper-checked{
  background-color: #ff0;
}
.row {
  cursor: pointer;
}
.row:hover {
  background: #e8f4fd;
  /* color: #fff; */
}
</style>



