<template>
    <main style="height: 100%;">
        <section class="center" style="height: 70px; background: #fff;">
            <div style="height: 75px; width: 96%; dispplay: flex; justify-content: space-between;">
                <div class="f-t1" style="margin-top:25px; font-weight: 500; font-size: 20px">Calendar</div>
            </div>
        </section>

        <section style="width: 100%; height: 100%; display: flex; align-items: center; justify-content: space-around;">
            <div class="appointment">
                <div class="calendar-container">
                    <div style="margin-top: 50px;height: 350px; display: flex; flex-direction: column; align-items: center;">
                        <div style="width: 420px;">
                            <div id='info' style='width: 100%; display: flex; justify-content: space-between;'>
                                <div @click='previousM' style="cursor: pointer;">
                                    <img src='../../assets/img/calLeft.png' alt='' style="width: 15px; height: 15px;">
                                </div>
                                <div>
                                    <span style="margin-right: 10px; font-size: 18px;">{{months[tableMonth]}}</span>
                                    <span>{{tableYear}}</span>
                                </div>
                                <div @click='nextM' style="cursor: pointer;">
                                    <img src='../../assets/img/calRight.png' alt='' style="width: 15px; height: 15px;">
                                </div>
                            </div>

                            <div class='calendar-head'>
                                <div class="head-group">Sun</div>
                                <div class="head-group">Mon</div>
                                <div class="head-group">Tue</div>
                                <div class="head-group">Wen</div>
                                <div class="head-group">Thu</div>
                                <div class="head-group">Fri</div>
                                <div class="head-group">Sat</div>
                            </div>

                            <div class="calendar">
                                <div v-for="e in firstWeekDay"  :key="e + tableMonth"  style="margin-right: -1px; margin-bottom: -1px;width: 61px; height: 45px; border: 1px solid #BFBFBF; "></div>
                                <div
                                    class="calendar-cell"
                                    v-for="(a) in (tableMonth===1 ? leapFeb :monDays[tableMonth].duration)"
                                    :key="a + tableYear"
                                    style="margin-right: -1px; margin-bottom: -1px;width: 61px; height: 45px; border: 1px solid #BFBFBF; display: flex; align-items: center;justify-content: space-around; cursor: pointer;"
                                    @click="getDate(a)"
                                    :style="{
                                        background: monDays[tableMonth].active.indexOf(a) >= 0 ?'#3E8EE6' :(((tableYear < today_year) || (tableYear === today_year && today_month > tableMonth) || (tableMonth === today_month && today_day > a) || ((a-firstSunday)%7==0)) ? '#ececec' : 'transparent'),
                                        color: monDays[tableMonth].active.indexOf(a) >= 0 ?'#fff' :(((tableYear < today_year) || (tableYear === today_year && today_month > tableMonth) || (tableMonth === today_month && today_day > a) || ((a-firstSunday)%7==0)) ? '#fff' : '#444')
                                    }"
                                >
                                    {{a}}
                                </div>
                                <div v-for="b in emptyCell" :key="b*100" style="margin-right: -1px; margin-bottom: -1px;width: 61px; height: 45px; border: 1px solid #BFBFBF;"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="date" style="margin-bottom:20px; flex-wrap: wrap-reverse; justify-content: space-between">
                    <div style = "flex: 3; display: flex">
                      <div style="flex: 1;">
                        <div style="margin-top: 50px; display: flex; flex-direction: column; align-items: center; ">
                            <div style="font-size: 18px;">Morning</div>
                            <div style="margin-top: 20px;">
                                <div class="center" style="cursor:pointer; margin-bottom: 15px; width: 124px; height: 40px; border: 1px solid #BFBFBF;"
                                v-for="(e, i) in schedule.slice(this.scheduleRange[0],8)" :key="'schedule'+i"
                                :style="{
                                        background: schedule[i+scheduleRange[0]].appt ? '#10B975': schedule[i+scheduleRange[0]].status?'#E1E1E1':'#FFFFFF',
                                        color: schedule[i+scheduleRange[0]].appt ? '#FFFFFF': '#000000',
                                }"
                                @click="changeBlockTime(schedule[i+scheduleRange[0]],i)">{{schedule[i+scheduleRange[0]].value | timeFormat}}</div>
                            </div>
                        </div>
                    </div>
                    <div style="flex: 1;">
                        <div style="margin-top: 50px; display: flex; flex-direction: column; align-items: center; ">
                            <div style="font-size: 18px;">Afternoon</div>
                            <div style="margin-top: 20px;">
                                <div class="center" style="cursor:pointer; margin-bottom: 15px; width: 124px; height: 40px; border: 1px solid #BFBFBF;"
                                v-for="(e, i) in schedule.slice(8,this.scheduleRange[1])" :key="'schedule'+i+8"
                                :style="{
                                        background: schedule[i+8].appt ? '#10B975': schedule[i+8].status?'#E1E1E1':'#FFFFFF',
                                        color: schedule[i+8].appt ? '#FFFFFF': '#000000',
                                }"
                                @click="changeBlockTime(schedule[i+8],i+8)"
                                >{{schedule[i+8].value | timeFormat }}</div>
                            </div>
                        </div>
                    </div>
                    <div style="flex: 1;">
                        <div v-if="chosenDay != 6" style="margin-top: 50px; display: flex; flex-direction: column; align-items: center; ">
                            <div style="font-size: 18px;">Evening</div>
                            <div style="margin-top: 20px;">
                                <div class="center" style="cursor:pointer; margin-bottom: 15px; width: 124px; height: 40px; border: 1px solid #BFBFBF;"
                                v-for="(e, i) in schedule.slice(18,schedule.length)" :key="'schedule'+i+18"
                                :style="{
                                  background: schedule[i+18].appt ? '#10B975': schedule[i+18].status?'#E1E1E1':'#FFFFFF',
                                  color: schedule[i+18].appt ? '#FFFFFF': '#000000',
                                }"
                                @click="changeBlockTime(schedule[i+18],i+18)"
                                >{{schedule[i+18].value | timeFormat}}</div>
                            </div>
                        </div>
                    </div>
                    <div v-if="!editable&&accountPermission === 'Admin'"  >
                      <div @click="editBt" class="center" style = "cursor: pointer; margin-right: 20px; margin-top: 20px; height: 30px; width: 90px; background-color: #4A90E2; color: #fff; font-size:14; font-weight: 500; border-radius: 4px; ">
                      Edit
                      </div>
                    </div>
                    <div v-if="editable" style="display: flex; flex: 1; margin-top: 20px">
                      <div class="center" @click="update" style= "cursor: pointer; margin-right: 20px; height: 30px; width: 90px; background-color: red; color: #fff; font-size:14; font-weight: 500; border-radius: 4px; ">Save</div>
                      <div class="center" @click="editBt" style= "cursor: pointer; height: 30px; width: 90px; background-color: #4A90E2; color: #fff; font-size:14; font-weight: 500; border-radius: 4px; ">Cancel</div>
                    </div>
                    </div>
                </div>
            </div>
        </section>
    </main>
</template>
<script>
export default {
  data() {
    return {
      businessType: 'OIL_CHANGE',
      scheduleRange: [0, 18],
      storeId: 'store003',
      blockTime: [],
      appTime: [],
      editable: false,
      activeDate: [1, 2, 3],
      months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      leapFeb: 28,
      priorDate: '',
      today: '',
      today_year: '',
      today_month: '',
      today_day: '',
      tableDay: '',
      tableYear: '',
      tableMonth: '',
      firstDay: '',
      firstDayDate: '',
      firstWeekDay: '',
      firstSunday: '',
      table: '',
      notSelect: false,
      active: false,
      tempSchedule: [
        {value: '0800', status: false, appt: false},
        {value: '0830', status: false, appt: false},
        {value: '0900', status: false, appt: false},
        {value: '0930', status: false, appt: false},
        {value: '1000', status: false, appt: false},
        {value: '1030', status: false, appt: false},
        {value: '1100', status: false, appt: false},
        {value: '1130', status: false, appt: false},

        {value: '1200', status: false, appt: false},
        {value: '1230', status: false, appt: false},
        {value: '1300', status: false, appt: false},
        {value: '1330', status: false, appt: false},
        {value: '1400', status: false, appt: false},
        {value: '1430', status: false, appt: false},
        {value: '1500', status: false, appt: false},
        {value: '1530', status: false, appt: false},
        {value: '1600', status: false, appt: false},
        {value: '1630', status: false, appt: false},

        {value: '1700', status: false, appt: false},
        {value: '1730', status: false, appt: false},
        {value: '1800', status: false, appt: false},
        {value: '1830', status: false, appt: false},
        {value: '1900', status: false, appt: false},
      ],
      monDays: [
        {duration: 31, active: []},
        {duration: 28, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
      ],
    }
  },
  filters: {
    timeFormat: function(v) {
      if (!v) return ''
      let h = v.slice(0, 2)
      let m = v.slice(2, 4)
      let category = 'AM'
      if (parseInt(h) === 12) {
        category = 'PM'
      } else if (parseInt(h) === 24) {
        h = 0
        category = 'AM'
      } else if (parseInt(h) > 12) {
        h = parseInt(h) - 12
        category = 'PM'
      } else {
        category = 'AM'
      }
      h = ('0' + h).slice(-2)
      return h + ':' + m + ' ' + category
    },
  },
  methods: {
    saveTime(time) {
      if (time.length === 0) {
        if (this.blockT.length !== 0) {
          this.$store.dispatch('calendar/delete', {
            storeId: this.storeId,
            businessType: this.businessType,
            dateStr: this.digitString(this.tableMonth + 1) + this.digitString(this.tableDay) + this.tableYear,
            times: this.blockT.toString(),
            accountId: '10000001',
          })
        }
      } else {
        this.$store.dispatch('calendar/update', {
          businessType: this.businessType,
          blockTime: time,
          storeId: this.storeId,
          dateStr: this.digitString(this.tableMonth + 1) + this.digitString(this.tableDay) + this.tableYear,
          userId: '10000001',
        })
      }
    },
    changeBlockTime(e, i) {
      if (!this.editable) return
      if (e.appt) return
      let index = this.blockTime.indexOf(e.value)
      if (index > -1) {
        this.blockTime.splice(index, 1)
        this.tempSchedule[i].status = false
      } else {
        this.blockTime.push(e.value)
        this.tempSchedule[i].status = true
      }
    },
    getBlockTime(month, day, year) {
      this.$store.dispatch('calendar/loadBlockTime', {
        month: this.digitString(month),
        day: this.digitString(day),
        year: this.digitString(year),
        storeId: this.storeId,
        userId: '10000001',
      })
    },
    digitString(v) {
      if (parseInt(v) < 10) {
        return '0' + v
      }
      return ''+v
    },
    update() {
      this.editable = !this.editable
      this.saveTime(this.blockTime)
    },
    editBt() {
      if (this.editable) {
        this.editable = !this.editable
        this.getBlockTime(this.tableMonth + 1, this.tableDay, this.tableYear)
      } else {
        this.blockTime = [...this.blockT]
        this.editable = !this.editable
      }
    },
    getCal() {
      this.tableMonth = this.today_month
      this.tableYear = this.today_year
    },
    tableCreate() {
      this.firstDay = new Date(this.tableYear, this.tableMonth, 1)
      if (this.tableYear % 4 === 0) {
        this.leapFeb = 29
      } else {
        this.leapFeb = 28
      };
      this.firstDayDate = this.firstDay.getDate()// 1
      this.firstWeekDay = this.firstDay.getDay()// 4
      if (this.firstWeekDay === 0) {
        this.firstSunday = this.firstDayDate
      } else {
        this.firstSunday = (7 - this.firstWeekDay) + this.firstDayDate
      }
    },

    nextM() {
      this.tableMonth++
      if (this.tableMonth === 12) {
        this.tableYear++
        this.tableMonth = 0
        this.tableCreate()
      } else {
        this.tableCreate()
      };
    },

    previousM() {
      this.tableMonth--
      if (this.tableMonth < 0) {
        this.tableYear--
        this.tableMonth = 11
        this.tableCreate()
      } else {
        this.tableCreate()
      }
    },
    getDate(e) {
      if (this.tableYear < this.today_year) return
      if (this.tableYear === this.today_year && this.today_month > this.tableMonth) return
      if (this.tableMonth === this.today_month && this.today_day > e) return
      if ((e - this.firstSunday) % 7 === 0) return
      this.monDays = [
        {duration: 31, active: []},
        {duration: 28, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
        {duration: 30, active: []},
        {duration: 31, active: []},
      ]
      let datechose = new Date(this.tableYear, this.tableMonth, e)
      this.chosenDay = datechose.getDay()
      this.scheduleRange = this.chosenDay === 6 ? [0, 17] : [0, 18]
      this.tableDay = e
      this.monDays[this.tableMonth].active.push(e)
      this.getBlockTime(this.tableMonth + 1, e, this.tableYear)
    //   this.getApptTime(this.tableMonth+1,e,this.tableYear)
    },
    pageBegin() {
      this.today = new Date()
      this.today_year = this.today.getFullYear()
      this.today_month = this.today.getMonth()
      this.today_day = this.today.getDate()
      this.tableYear = this.today_year
      this.firstDay = new Date(this.today_year, this.today_month, 1)
      this.firstDayDate = this.firstDay.getDate()
      this.firstWeekDay = this.firstDay.getDay()
      if (this.firstWeekDay === 0) {
        this.firstSunday = this.firstDayDate
      } else {
        this.firstSunday = (7 - this.firstWeekDay) + this.firstDayDate
      }
      if (this.today_year % 4 === 0) {
        this.leapFeb = 29
      } else {
        this.leapFeb = 28
      }
    },
  },
  created() {
    this.pageBegin()
    this.getCal()
    this.getDate(this.today_day)
  },
  computed: {
    schedule() {
      // 需要加入type判断时间表
      // variable name need to be changed
      let today = new Date()
      let curTime = today.getMinutes() > 30 ? (today.getHours() + 1) * 100 + 30 : today.getHours() * 100 + today.getMinutes() + 30
      let schedules = this.tempSchedule
      let status = this.tableDay === this.today_day && this.tableMonth === this.today_month && this.today_year === this.tableYear
      schedules.forEach(e => {
        e.appt = this.$store.state.calendar.appointmentT.indexOf(e.value) !== -1
        e.status = ((status && parseInt(e.value) < curTime) || (this.$store.state.calendar.blockT.indexOf(e.value) !== -1 && !e.appt))
      })
      this.tempSchedule = schedules
      return schedules
    },
    blockT() { return this.$store.state.calendar.blockT },
    emptyCell() {
      let empty = 7 - (this.firstWeekDay + this.monDays[this.tableMonth].duration) % 7
      return empty !== 7 ? empty : 0
    },
    accountPermission() { return this.$store.state.me.accountPermission },
  },
  mounted() {

  },

}
</script>

<style scoped>
.calendar-head {
    display: flex;
    width: 100%;
    text-align:center;
    margin-top: 20px;
}
.head-group {
    width: 70px;
    text-align: center;
    color: #BFBFBF;
}

.calendar {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    max-height: 300px;
    text-align:center;
    margin-top: 20px;
}
.calendar-cell {
  transition: all 0.2s ease-in;
}
.calendar-container {
    flex: 2
}
.date {
    flex: 3;
    display: flex;
}
.appointment {
    margin: 26px auto;
    width: 96%;
    background: #fff;
    display: flex;
}
@media only screen and (max-width: 1250px) {
    .appointment {
        flex-direction: column;
        align-items: center;
    }
    .calendar-container {
        flex: 1;
        width: 100%;
    }
    .date {
        flex: 1;
        width: 100%;
    }
}
</style>



