
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
import Config from 'sdk/hiveel-Config'
export default class AccountManagementService {
  static registerReadOnly(accountInfo) {
    let url
    let formData = new FormData()
    url = Config.authServer + '/auth/merchants'
    formData.append('userName', accountInfo.userName)
    formData.append('password', accountInfo.password)
    formData.append('role', 'merchant')
    formData.append('permission', accountInfo.permission)
    formData.append('accountPermission', 'readOnly')
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }
  static load() {
    let url
    url = Config.authServer + '/auth/merchants'
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
  static delete(e) {
    let url
    url = Config.authServer + '/auth/merchants/' + e.id + '/disable'
    let options = {
      method: 'PUT',
    }
    return AjaxUtil.run(url, options)
  }
}



