<template>
  <main>
    <section class="userInfoResponsive"  style="background: #fff;">
      <div style="display: flex; flex-wrap: wrap-reverse; justify-content: space-between; width: 96%; padding: 0 25px">
        <div style="display: flex; align-items: center; justify-content: space-between; margin: 25px 0">
          <div style="margin-right:20px"><v-avatar class="bg-green" shape="square" size="large" icon="user"></v-avatar></div>
          <div class="f-t1">{{me.userName}}</div>
        </div>
        <div style = "display:flex; flex-direction: column; margin: 25px 0">
          <div class="c-grey" style="font-size: 18px; text-align: right;">User#{{me.employee.id}}</div>
          <div style="font-size:18px; text-align: right;" class="f-t2">Status：<span class="c-blue">{{me.accountPermission}}</span></div>
        </div>
      </div>
    </section>
    <div class="bg-white" style="margin: 20px auto auto auto; width: 96%; background: #fff;">
      <div class="center" style="width: 100%; height: 50px; border-bottom: 1px solid #dfdfdf;">
        <div style="width: 96%; display: flex; align-items: center;">
          <span class="f-t1">User Information</span>
        </div>
      </div>
      <div style="margin: 0 auto; width: 100%; background: #fff;">
        <div style="width: 100%;  display: flex; flex-direction: column; justify-content: center; align-items: center;">
          <div style="width: 100%; display: flex;">
            <section style="margin: 20px auto 10px; flex-basis: 450px; display: flex; justify-content: space-between; align-items: center; ">
              <div style="height: 32px;">Profile Picture:</div>
              <div style="width: 80%; display: flex; justify-content: space-around; align-items: center;">
                <div style="width: 100px; display: flex; justify-content: center;">
                  <avatar width="70" height="70" :src="me.employee.imageUrl"></avatar>
                </div>
                <div>
                  <label class="center bg-grey-white" style="width: 100px; height: 30px;">
                    Choose File<input style="display: none;" type="file" @change="readPic">
                  </label>
                </div>
              </div>
            </section>
          </div>
          <div style="display: flex; width: 100%; background: #fff;">
            <v-form style="margin: 10px auto; flex-basis: 350px;" direction="horizontal" :model="form" :rules="rules" ref="form">
              <section style="width: 350px; display: flex; justify-content: space-between;">
                <div class="center" style="height: 32px;">Email:</div>
                <v-form-item style="width: 80%" prop="email" has-feedback>
                  <v-input type="text" size="large" v-model="me.employee.email" placeholder=""></v-input>
                </v-form-item>
              </section>
              <section style="width: 350px; display: flex; justify-content: space-between;">
                <div class="center" style="height: 32px;">Phone:</div>
                <v-form-item style="width: 80%" prop="phoneNumber" has-feedback>
                  <v-input type="text" size="large" v-model="me.employee.phoneNumber" placeholder=""></v-input>
                </v-form-item>
              </section>
              <section style="width: 350px; display: flex; justify-content: space-between;">
                <div class="center" style="height: 32px;">Password:</div>
                <v-form-item style="width: 80%" prop="password" has-feedback>
                  <v-input type="text" size="large" v-model="me.employee.password" placeholder=""></v-input>
                </v-form-item>
              </section>
              <section class="center" style="height: 100px;">
                <button class="btn-green" type="button" style="width: 160px; height: 40px; display:flex; justify-content: center;"  @click="update">Submit</button>
              </section>
            </v-form>
          </div>
        </div>
      </div>
    </div>







 
  </main>
</template>
<script>
import avatar from 'components/image/avatar'
export default {
  data() {
    var checkPhoneNumber = (rule, value, callback) => {
      var phoneRe = /^(()?\d{3}())?(-|\s)?\d{3}(-|\s)?\d{4}$/
      setTimeout(() => {
        if (!phoneRe.test(value)) {
          callback(new Error('please input correct phone number'))
          return
        }
        if (!value) {
          callback(new Error('please input phone number'))
        }
        callback()
      }, 1000)
    }
    var checkNickName = (rule, value, callback) => {
      if (!value) {
        callback(new Error('please input the name!!'))
        return
      }
      callback()
    }
    return {
      rules: {
        nickName: [
          { required: true, message: 'please input the name' },
          { validator: checkNickName },
        ],
        email: [
          { required: true, message: 'please input email address', trigger: 'blur' },
          { type: 'email', message: 'please input correct email address', trigger: 'blur,change' },
        ],
        phoneNumber: [
          { required: true, message: 'please input phone number' },
          { validator: checkPhoneNumber },
        ],
      },
      labelCol: { span: 5 },
      wrapperCol: { span: 19 },
    }
  },
  computed: {
    me() { return this.$store.state.me },
  },
  components: {
    avatar,
  },
  methods: {
    async readPic() {
      let reader = new FileReader()
      reader.onload = () => {
        this.$store.dispatch('me/updateAvatar', reader.result)
      }
      reader.readAsDataURL(event.target.files[0])
    },
    update(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        this.$store.dispatch('me/update')
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
  },
}
</script>
<style>
@media only screen and (max-width: 600px) {
  .userInfoResponsive {
    height: 100%;
    background:brown;
  }
}

.personInfo {
  width: 6%;
}
</style>



