
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
import Config from 'sdk/hiveel-Config'

export default class {
  static load(e) {
    let url = ''
    url = Config.sitebase + '/hiveelpay/api/biz/merchant/appointment/search'
    // url = '/hiveelpay/api/biz/merchant/appointment/search'
    let formData = new FormData()
    formData.append('mchId', e.userId)
    formData.append('perPageSize', e.limit)
    if (e.keyWord) formData.append('keyWords', e.keyWord)
    if (e.curr) formData.append('currentPage', e.curr)
    if (e.total) formData.append('total', e.curr !== 1 ? e.total : 0)
    if (e.storeId) formData.append('storeId', e.storeId)
    if (e.appointmentStatus) formData.append('appointmentStatus', e.appointmentStatus)
    if (e.type) formData.append('businessType', e.type)
    if (e.startDateStr) formData.append('startDateStr', e.startDateStr + '0000')
    if (e.endDateStr) formData.append('endDateStr', e.endDateStr + '0000')
    if (e.tradeInType) formData.append('tradeInType', e.tradeInType)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }
  static updateStatus(e){
    let url = Config.sitebase + `/hiveelpay/api/biz/merchant/appointment/status/update/${e.appointmentId}/${e.toMchId}/${e.appointmentStatus}`
    let options = {
      method: 'PUT',
    }
    return AjaxUtil.run(url, options)
  }
  static cancelCarMax(e){
    let url = Config.sitebase + `/hiveelpay/api/biz/merchant/appointment/cancel/${e.appointmentId}`
    let formData = new FormData()
    formData.append('comment', e.comment)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }
  
  static update(e) {
    let url = Config.sitebase + '/hiveelpay/api/biz/customer/appointment/update'
    let formData = new FormData()
    formData.append('appointmentId', e.appointmentId)
    formData.append('firstName', e.firstName)
    formData.append('lastName', e.lastName)
    formData.append('appointmentDateTimeStr', e.appointmentDateTimeStr)
    formData.append('appointmentStatus', e.appointmentStatus)
    formData.append('businessType', e.businessType)
    formData.append('email', e.email)
    formData.append('phoneNumber', e.phoneNumber)
    formData.append('street', e.street ? e.street : '')
    formData.append('apt', e.apt ? e.apt : '')
    formData.append('city', e.city ? e.city : '')
    formData.append('state', e.state ? e.state : '')
    formData.append('customerId', e.customerId)
    formData.append('toMchId', e.toMchId)
    formData.append('toStoreId', e.toStoreId)
    formData.append('zipcode', e.zipcode ? e.zipcode : '')
    formData.append('vin', e.vin)
    formData.append('plateState', e.plateState)
    formData.append('year', e.year)
    formData.append('make', e.make)
    formData.append('model', e.model)
    formData.append('mileage', e.mileage ? e.mileage : '')
    formData.append('previousOfferPrice', e.previousOfferPrice ? e.previousOfferPrice : 0)
    formData.append('titleStatus', e.titleStatus ? e.titleStatus : '')
    formData.append('companyName', e.companyName ? e.companyName : '')
    formData.append('whereCarAndKey', e.whereCarAndKey ? e.whereCarAndKey : '')
    formData.append('remark', e.remark ? e.remark : '')
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)
  }
  static loadById(e) {
    let url = Config.sitebase + '/hiveelpay/api/biz/merchant/appointment/' + e.userId + '/' + e.id
    // let url = '/hiveelpay/api/biz/merchant/appointment/' + e.userId + '/' + e.id
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
  static quote(e){
    let url = ''
    url = Config.sitebase +'/hiveelpay/api/biz/merchant/appointment/quote'
    // url = '/hiveelpay/api/biz/merchant/appointment/quote'
    let formData = new FormData()
    formData.append('appointmentId', e.appointmentId)
    formData.append('price', e.price)
    let options = {
      method: 'POST',
      body: formData,
    }
    return AjaxUtil.run(url, options)    
  }
}



