
import AjaxUtil from 'sdk/hiveel-AjaxUtil'
import ParamUtil from 'sdk/hiveel-ParamUtil'
import Config from 'sdk/hiveel-Config'
export default class {
  static loadBlockTime(e) {
    if (e.type === undefined) {
      e.type = ''
    }
    console.log(e)
    let url = Config.sitebase + '/hiveelpay/api/biz/merchant/blocked/time/' + e.userId + '/' + e.month + e.year + '/' + e.storeId + '/' + e.day
    let options = {
      method: 'GET',
    }
    return AjaxUtil.run(url, options)
  }
  static delete(e) {
    let url = Config.sitebase + '/hiveelpay/api/biz/merchant/block/time?' + ParamUtil.createUrlEncode(e)
    let options = {
      method: 'DELETE',
    }
    return AjaxUtil.run(url, options)
  }
  static update(e) {
    let formData = new FormData()
    formData.append('accountId', e.userId)
    formData.append('businessType', e.businessType)
    formData.append('dateStr', e.dateStr)
    formData.append('times', e.blockTime)
    formData.append('storeId', e.storeId)
    let options = {
      method: 'POST',
      body: formData,
    }
    let url = Config.sitebase + '/hiveelpay/api/biz/merchant/block/time'
    return AjaxUtil.run(url, options)
  }
}



