<template>
    <main>
        <div v-if="this.permission == 4"><carService></carService></div>
        <div v-if="this.userName === 'hiveeltradein' || this.userName === 'service5'"><tradeIn></tradeIn></div>
        <div v-if="this.userName === 'bidlanetest1' || this.userName === 'hiveelbidlane'"><newTradeInMain :storeIdList="['Pasadena0','WoodlandHills0','Burbank0','LAX0','WestLosAngeles0','Irvine0','CostaMesa0']"></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest2' || this.userName === 'hiveelbidlane1'"><newTradeInMain :storeIdList="['Pasadena0','WoodlandHills0']" ></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest3' || this.userName === 'hiveelbidlane2'"><newTradeInMain :storeIdList="['Burbank0']" ></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest5' || this.userName === 'hiveelbidlane4'"><newTradeInMain :storeIdList="['WoodlandHills0']" ></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest6' || this.userName === 'hiveelbidlane5'"><newTradeInMain :storeIdList="['LAX0']" ></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest7' || this.userName === 'hiveelbidlane6'"><newTradeInMain :storeIdList="['WestLosAngeles0']" ></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest8' || this.userName === 'hiveelbidlane7'"><newTradeInMain :storeIdList="['Irvine0','CostaMesa0']" ></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest9' || this.userName === 'hiveelbidlane8'"><newTradeInMain :storeIdList="['CostaMesa0']" ></newTradeInMain></div>
        <div v-if="this.userName === 'bidlanetest10' || this.userName === 'hiveelbidlane9'"><newTradeInMain :storeIdList="['CarmaxVirtual0']"></newTradeInMain></div>
    </main>
</template>
<script>
import newTradeInMain from './newTradeIn/mainCalendar'
import tradeIn from './tradeInCalendar'
import carService from './serviceCalendar'
export default {
  data() {
    return {
    }
  },
  components: {
    tradeIn, carService,newTradeInMain
  },
  computed: {
    permission() { return this.$store.state.me.permission },
    userName(){return this.$store.state.me.userName}
  },
  created() {
  },
  mounted() {
  },
  methods: {
  },
}
</script>



