<template>
    <main style="height: 100%;">
        <section class="center" style="height: 70px; background: #fff;">
            <div style="height: 75px; width: 96%; dispplay: flex; justify-content: space-between;">
                <div class="f-t1" style="margin-top:25px; font-weight: 500; font-size: 20px">Calendar</div>
            </div>
        </section>
      <div v-if="storeIdList&&storeIdList[0] === 'CarmaxVirtual0'">
        <section style = "margin: 26px auto; width: 96%; background: #fff; display: flex; align-items: center; flex-direction: column">
        <div style="width: 100%; height:60px; padding-left: 26px; background: #fff; display: flex; align-items: center; border-bottom: 1px solid #E9E9E9; font-size:16px; font-weight:500; ">Carmax Calendar</div>
        <div class="f-12" style ="height: 32px; margin: 35px 0">You account don't have permission to access calendar</div>
        </section>
      </div>
      <div v-else style="width background-color: #fff; overflow: auto;">
        <tab  :tabList="tabList" :active="tab" :changeTab="changeTab" ></tab>
        <div v-for="(e, i) in tabList" :key="'tablist'+i">
           <newTradeInSub v-if="tab===e.key" :storeId="e.value" userId="10000003"></newTradeInSub>
        </div>
     </div>
    </main>
</template>
<script>
import newTradeInSub from './subCalendar'
import tab from 'components/tab'
export default {
  props: [
    'storeIdList',
  ],
  data() {
    return {
    tabList: [],
    }
  },
  components: {
    newTradeInSub, tab
  },
  computed:{
      tab() {return this.$store.state.tabControl.appointment}
  },
  created() {
    this.storeIdList.forEach(
      (e,i)=>this.tabList.push({ name: e.replace('0',''), key: i+1+'',value:e })
    )
  },
  filters: {
    timeFormat: function(v) {
      if (!v) return ''
      let h = v.slice(0, 2)
      let m = v.slice(2, 4)
      let category = 'AM'
      if (parseInt(h) === 12) {
        category = 'PM'
      } else if (parseInt(h) === 24) {
        h = 0
        category = 'AM'
      } else if (parseInt(h) > 12) {
        h = parseInt(h) - 12
        category = 'PM'
      } else {
        category = 'AM'
      }
      h = ('0' + h).slice(-2)
      return h + ':' + m + ' ' + category
    },
  },
  methods: {
    changeTab(e) {
      this.$store.dispatch('tabControl/changeAppointment', e)
    },
  },
}
</script>

<style scoped>
.calendar-head {
    display: flex;
    width: 100%;
    text-align:center;
    margin-top: 20px;
}
.head-group {
    width: 70px;
    text-align: center;
    color: #BFBFBF;
}

.calendar {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    max-height: 300px;
    text-align:center;
    margin-top: 20px;
}
.calendar-cell {
  transition: all 0.2s ease-in;
}
.calendar-container {
    flex: 2
}
.date {
    flex: 2;
    display: flex;
}
.appointment {
    margin: 26px auto;
    width: 96%;
    background: #fff;
    display: flex;
}
@media (max-width: 1250px) {
    .appointment {
        flex-direction: column;
        align-items: center;
    }
    .calendar-container {
        flex: 1;
        width: 100%;
    }
    .date {
        flex: 1;
        width: 100%;
    }
}
</style>



