import Vue from 'vue'

const CLEAN_STATE = {
  show: false,
}

const state = Object.assign({}, CLEAN_STATE)

const actions = {
  async fail({commit, dispatch, state, rootState}, payload, config = {}) {
    if (payload.code === 'UNAUTHORIZED') {
      dispatch('me/logout', {}, {root: true})
    }
    if (state.show) {
      return
    }
    let content = getContent(payload.code)
    Vue.$modal.error({...content, onOk: () => { commit('show', false) }})
    commit('show', true)
  },
}

const mutations = {
  show(state, e) {
    state.show = e
  },
}

function getContent(code) {
  switch (code) {
    case '-1': return {title: 'connection fail', content: ''}
    case 'UNAUTHORIZED': return {title: 'UNAUTHORIZED to use', content: ''}
    case 'PARAMETER': return {title: 'param erorr', content: ''}
    case 'FAIL': return {title: 'operation fail', content: ''}
    case 'VIN_NOT_FOUND': return {title: 'vin not found', content: ''}
    case 'USERNAME_PASSWORD_INCORRECT': return {title: 'username password incorrect', content: ''}
    case 'OLD_PASSWORD_INCORRECT': return {title: 'old pass incorrect', content: ''}
    default: return {title: code}
  }
}
export default {
  namespaced: true, state, actions, mutations,
}



